import { useSelector } from 'react-redux';
import { themeAlpine, colorSchemeDark, colorSchemeLight, iconOverrides } from 'ag-grid-community';
import useMUITheme from './useMUITheme';

const useAGGridTheme = ({noCellPadding=false}) => {
  const mode = useSelector((state) => state.settings.mode) || 'light';
  const muiTheme = useMUITheme();
  const tableSpacing = 2;

  const fontAwesomeIcons = iconOverrides({
    type: 'font',
    family: 'Font Awesome 6 Free',
    cssImports: ['https://use.fontawesome.com/releases/v6.7.0/css/all.css'],
    weight: 'bold', // Font Awesome requires bold font weight
    icons: {
        // use font codes documented by Font Awesome e.g. '\u{f062}' == arrow-up
        asc: '\u{f062}',
        desc: '\u{f063}',
        none: '\u{f0dc}',
    },
})
  const params = {
    spacing: tableSpacing,
    fontSize: muiTheme.typography.fontSize,
    cellTextColor: muiTheme.palette.text.secondary,
    backgroundColor: muiTheme.palette.background.paper,
    headerBackgroundColor: muiTheme.palette.background.pinned,
    headerTextColor: muiTheme.palette.text.primary,
    selectedRowBackgroundColor: 'inherit'
  };

  if (noCellPadding) {
    params.cellHorizontalPaddingScale = 0;
    params.cellVerticalPaddingScale = 0;
  }

  return themeAlpine.withPart(mode === 'dark' ? colorSchemeDark : colorSchemeLight).withPart(
    fontAwesomeIcons
  ).withParams(params);
}

export default useAGGridTheme;