import { IconButton } from "@mui/material";
import React from "react";
import { useDispatch } from "react-redux";
import { updateSettings } from "@/store/settingsSlice";
import useSetting from "@/hooks/useSetting";
import BugReportOutlinedIcon from '@mui/icons-material/BugReportOutlined';

export const IconEnableDebugging = React.memo(function IconEnableDebugging() {
    const dispatch = useDispatch();
    const {value} = useSetting('enableDebugging');

    const icon = <BugReportOutlinedIcon/>;

    const handleClick = () => {
        dispatch(updateSettings({enableDebugging: !value}));

    }

    return (
      <IconButton size="small"
        onClick={handleClick}
        sx={{
          color: value ? 'primary.light' : 'text.secondary',
          '&:hover': {
              color: value ? 'primary.main' : 'text.primary',
          }
        }}
        >
          {React.cloneElement(icon, {fontSize: 'small'})}
        </IconButton>
    );
  });