import React from 'react';
import { AGGridResponsiveContainer } from '@/components/elements/displays';
import { AgGridReact } from 'ag-grid-react';
import { useRef, useMemo, useCallback, useState } from 'react';
import useAGGridTheme from '@/hooks/useAGGridTheme';
import { Box } from '@mui/material';
import _ from 'lodash';
import { MiniJsonBox } from '@/components/elements/JsonBox';

import { AllCommunityModule, ModuleRegistry } from 'ag-grid-community';
import { isMissing } from '@/utils/helpers';
import { useEffect } from 'react';

ModuleRegistry.registerModules([AllCommunityModule]);


export const BasicTable = React.memo(function BasicTable({initRowData, overridingColDefs={}, overridingDefaultColDef={}, overridingGridOptions={}}) {
    const agGridTheme = useAGGridTheme({});
    const [rowData, setRowData] = useState([]);
    const gridRef = useRef(null);

    useEffect(() => {
        setRowData([...initRowData]);
    }, [initRowData]);

    const defaultColDef = useMemo(() => ({
        sortable: true,
        resizable: true,
        autoHeight: true,
        maxWidth: 600,
        minWidth: 50,
        cellRenderer: (params) => {
            if (!isMissing(params.value) && typeof params.value === 'object') {
                return <MiniJsonBox obj={params.value} />;
            }
            return String(params.value);
        },
        ...overridingDefaultColDef,

    }), [overridingDefaultColDef]);

    const columnNames = useMemo(() => {
        return _.union(...(rowData || []).map(item => _.keys(item)));
    }, [rowData]);

    const columnDefs = useMemo(() => {
        return columnNames.map((columnName) => ({
            field: columnName,
            headerName: columnName,
            ...(overridingColDefs[columnName] || {}),
        }));
    }, [columnNames, overridingColDefs]);

    const onGridReady = useCallback((params) => {
        params.api.sizeColumnsToFit();
    }, []);


    const handleGridSizeChanged = useCallback((params) => {
        params.api.sizeColumnsToFit();
    }, []);

    const handleRowDataUpdated = useCallback((params) => {
        params.api.sizeColumnsToFit();
    }, []);



    return (
        <Box sx={{display: 'flex', flex: 1, flexDirection: 'column', height: '100%', width: '100%'}}>
            <AGGridResponsiveContainer>
                <AgGridReact
                    domLayout='normal'
                    ref={gridRef}
                    theme={agGridTheme}
                    rowData={rowData}
                    columnDefs={columnDefs}
                    onGridReady={onGridReady}
                    onGridSizeChanged={handleGridSizeChanged}
                    defaultColDef={defaultColDef}
                    pagination={true}
                    onRowDataUpdated={handleRowDataUpdated}
                    {...overridingGridOptions}
                />
            </AGGridResponsiveContainer>
        </Box>
    );
});
