import React from 'react';
import _ from 'lodash';
import path from 'path-browserify';
import { ChipList } from '@/components/elements/displays';
import { TextSegments } from '@/components/elements/displays';

const ChildrenStats = React.memo(function ChildrenStats({items}) {
    const countFolder = _(items).filter('isDir').size();
    const countFiles = _(items).filter(['isDir', false]).countBy(it => path.extname(it.id)).value();
    const records = Object.entries({
        ...(countFolder > 0 ? {folder: countFolder} : {}),
        ...countFiles
    });


    return (
        <ChipList items={records.map(([key, value]) => (
            <TextSegments key={key} variant="body2" segments={[
                {text: key === 'folder' ? 'Subfolders' : `*${key}`, sx: {fontFamily: 'monospace'}},
                {text: `x ${value.toLocaleString()}`, sx: {color: 'text.secondary'}},
            ]} />
        ))} />
    );
});

export default ChildrenStats;