import React from 'react';
import { Box } from '@mui/material';
import { parseFilterItem } from '@/utils/sql/parseFilterItem';
import { ReadOnlySQL, SectionHeader } from '@/components/elements/displays';
import { map, size } from 'lodash';
import ArrowRightAltIcon from '@mui/icons-material/ArrowRightAlt';
import ClickCard from '@/components/elements/ClickCard';
import useFilters from '@/hooks/useFilters';



const FiltersArrow = () => {
  const { filters, extraFilter, hasFilters } = useFilters();


  const details = (
    <>
      <SectionHeader title={`${size(filters)} columns with filters`}/>
      {map(filters, (ft, columnName) => (
        <Box key={columnName}>
          <ReadOnlySQL value={parseFilterItem(columnName, ft)}/>
        </Box>
      ))}

      <SectionHeader title="Extra filter"/>
      <Box>
        <ReadOnlySQL value={extraFilter}/>
      </Box>

    </>
  );

  return (
    <ClickCard trigger={<ArrowRightAltIcon />} title="Filters" tooltip='Filters'>
      {details}
    </ClickCard>
  );
};

export default FiltersArrow;