import _ from 'lodash';
import { Typography, Box } from '@mui/material';
import React from 'react';

export const highlightMatch = (str, search) => {
    if (!search) {
        return <Typography sx={{color: 'inherit', bgcolor: 'transparent'}} className='truncate'>{str}</Typography>;
    } else if (!str.includes(search)) {
        return <Typography sx={{color: 'inherit', bgcolor: 'transparent'}} className='truncate'>{str}</Typography>;
    } else {
        const regex = new RegExp(`(${search})`, 'ig');
        const parts = str.split(regex);

        return (
          <Box sx={{display: 'flex', flexDirection: 'row', gap: 0}}>
            {parts.map((part, index) =>
              part.toLowerCase() === search.toLowerCase() ? (
                <Typography key={index} sx={{bgcolor: 'warning.main', color: 'warning.contrastText'}} className='truncate'>{part}</Typography>
              ) : (
                <Typography key={index} sx={{color: 'inherit', bgcolor: 'transparent'}} className='truncate'>{part}</Typography>
              )
            )}
          </Box>
        );
    }
}

export const truncateText = (text, maxWidth = 100) => {
    const s = String(text);
    const maxNumChars = maxWidth / 7; // A rough estimate of the number of characters for given width
    return s.length > maxNumChars ? s.substring(0, maxNumChars) + '.' : s;
}


function charPrefixSimilarity(a, b) {
  const len = Math.min(a.length, b.length);
  let common = 0;

  for (let i = 0; i < len; i++) {
    if (a[i] === b[i]) {
      common++;
    } else {
      break;
    }
  }

  const totalLen = Math.max(a.length, b.length);
  return common / totalLen;
}


function commonPrefix(strings) {
  if (!strings.length) return '';

  const shortest = _.minBy(strings, s => s.length);

  let i = 0;
  while (
    i < shortest.length &&
    _.every(strings, s => s[i] === shortest[i])
  ) {
    i++;
  }

  return shortest.slice(0, i);
}

export function clusterByPrefix(strings, threshold = 0.6) {
  if (!strings || strings.length === 0) {
    return {};
  }

  const visited = new Set();
  const clusters = [];

  for (let i = 0; i < strings.length; i++) {
    if (visited.has(i)) continue;
    const cluster = [strings[i]];
    visited.add(i);

    for (let j = i + 1; j < strings.length; j++) {
      if (visited.has(j)) continue;
      const sim = charPrefixSimilarity(strings[i], strings[j]);
      if (sim >= threshold) {
        cluster.push(strings[j]);
        visited.add(j);
      }
    }

    clusters.push(cluster);
  }
  const groups = _.filter(clusters, g => g.length > 1);
  const clusterMapping = _(strings).map(s => [s, '~ungrouped~']).fromPairs().value();
  _.forEach(groups, (group, index) => {
    const prefix = commonPrefix(group);
    _.forEach(group, s => {
      clusterMapping[s] = `${prefix}*`;
    });
  });
  return clusterMapping;
}