import { columnMetaSlice } from './dataSlice';
import { runQuery } from '@/utils/api';
import _ from 'lodash';
import { matchDerivedColumns, addDerivedColumn } from '@/store/derivedColumnsSlice';

function normalizeType(type) {
  return type.toUpperCase().replace(/\(\d+,?\d*\)/, ''); // Remove parameters like in DECIMAL(10,2)
}

const DUCKDB_TYPES = {
  INTEGER: [ 'TINYINT', 'SMALLINT', 'INTEGER', 'BIGINT', 'HUGEINT',
    'UTINYINT', 'USMALLINT', 'UINTEGER', 'UBIGINT'],
  BOOLEAN: ['BOOLEAN'],
  FLOAT: ['FLOAT', 'DOUBLE', 'DECIMAL', 'REAL'],
  STRING: ['VARCHAR', 'CHAR', 'STRING', 'TEXT', 'BINARY'],
  DATETIME: ['TIMESTAMP', 'DATE', 'TIME', 'TIMESTAMP_NS'],
}


function computeTypeShortcuts(type) {
  const normalizedType = normalizeType(type);
  const isInteger = DUCKDB_TYPES.INTEGER.includes(normalizedType);
  const isFloat = DUCKDB_TYPES.FLOAT.includes(normalizedType);
  const isString = DUCKDB_TYPES.STRING.includes(normalizedType);
  const isNumeric = isInteger || isFloat;
  const isBoolean = DUCKDB_TYPES.BOOLEAN.includes(normalizedType);
  const isDatetime = DUCKDB_TYPES.DATETIME.includes(normalizedType);
  const agType = isNumeric ? 'number' : isBoolean ? 'boolean' : isDatetime ? 'dateString' : 'text';  // TODO: deal with date
  const isPrimitive = isNumeric || isBoolean || isString;

  return {isInteger, isFloat, isNumeric, isBoolean, isString, isPrimitive, agType, isDatetime};
}

function parseValue(typeShortcuts, valueString) {
  if (typeShortcuts.isInteger) {
    return parseInt(valueString);
  } else if (typeShortcuts.isFloat) {
    return parseFloat(valueString);
  } else if (typeShortcuts.isBoolean) {
    return valueString === 'true';
  } else if (typeShortcuts.isString) {
    return valueString;
  } else {
    return valueString;
  }
}

const parseColumnMeta = (data) => {
  return _(data).map(item => {
    const typeShortcuts = computeTypeShortcuts(item.column_type);
    const hasNull = item.cntNull > 0;
    const allNull = item.cntAll > 0 && item.cntAll === item.cntNull;
    const meta = {
      name: item.column_name,
      duckdbType: item.column_type,
      singleValue: item.singleValue,
      ...typeShortcuts,
      min: parseValue(typeShortcuts, item.min),
      max: parseValue(typeShortcuts, item.max),
      isDerived: false,
      hasNull,
      allNull,
      ..._.pick(item, ['cntAll', 'cntNull', 'maxCntDPerGroup', 'isPlainEncoding'])
    };
    return [item.column_name, meta];
  }).fromPairs().value();
};


export const fetchParquetMeta = async ({dispatch, tableExpr, sqlKey}) => {
  const metaExpr = tableExpr.replace('read_parquet', 'parquet_metadata');
  const conditionExcludeListDict = `
    path_in_schema NOT LIKE '%, key_value, %' 
    AND path_in_schema NOT LIKE '%, list, element%'
  `;
  const queryStructColumns = `
    SELECT DISTINCT REPLACE(path_in_schema, ', ', '.') AS column_name
    FROM ${metaExpr}
    WHERE path_in_schema LIKE '%, %' AND ${conditionExcludeListDict}
    `;

  const structColumnsData = await runQuery({dispatch, key: sqlKey, query: queryStructColumns, transformResult: 'dictOfLists'});
  const structColumns = (structColumnsData?.column_name || []).filter(c => !c.includes(' '));


  const allColumnsExpr = ['*', ..._.map(structColumns, c => `${c} AS "${c}"`)].join(',');


  const query = `WITH meta AS (
    SELECT 
        REPLACE(path_in_schema, ', ', '.') AS column_name, ---  TODO column name may contain spaces
        SUM(num_values) AS cntAll,
        SUM(stats_null_count) AS cntNull,
        MIN(stats_min_value) AS min,
        MAX(stats_max_value) AS max,
        MAX(stats_distinct_count) AS maxCntDPerGroup,
        BOOL_AND(encodings = 'PLAIN') AS isPlainEncoding
    FROM ${metaExpr}
    WHERE ${conditionExcludeListDict}
    GROUP BY path_in_schema
  )
  SELECT column_name, column_type, cntAll,
    --- Sometimes parquet metadata may be wrong for columns with all null values
    (CASE WHEN (min IS NULL AND max IS NULL) THEN cntAll WHEN ((min IS NULL OR max IS NULL) AND cntNull = 0) THEN cntAll ELSE cntNull END) AS cntNull,
    min, max, maxCntDPerGroup, isPlainEncoding,
    (min = max AND min IS NOT NULL) AS singleValue
  FROM (DESCRIBE SELECT ${allColumnsExpr} FROM ${tableExpr})
      LEFT JOIN meta USING (column_name)
  `;
  return await runQuery({dispatch, key: sqlKey, query: query});
}

export const fetchCsvMeta = async ({dispatch, tableExpr, sqlKey}) => {
  const query = `
  SELECT column_name, column_type, count AS cntAll, CEIL(count * null_percentage) AS cntNull, min, max
  FROM (SUMMARIZE SELECT * FROM ${tableExpr})
  `;
  return await runQuery({dispatch, key: sqlKey, query: query});
}

export const fetchColumnMeta = () => async (dispatch, getState) => {
  const { data, loading, error, shouldRefresh } = getState().columnMeta || { data: null, loading: false, error: null, shouldRefresh: false };

  if ((data || loading || error) && !shouldRefresh) return; //  Already loading or loaded  skip!


  dispatch(columnMetaSlice.actions.fetchStart());

  try {
    const { tableExpr, fileFormat } = getState().dataFile;
    const categoricalThreshold = getState().settings.thresholdCategorical;
    const sqlKey = 'columnMeta';
    const fetcher = {
      parquet: fetchParquetMeta,
      csv: fetchCsvMeta,
    }[fileFormat];
    const data = await fetcher({dispatch, tableExpr, sqlKey, categoricalThreshold});

    const initialOrder = _.map(data, 'column_name');
    const meta = parseColumnMeta(data);
    const countRows = data?.[0]?.cntAll;
    const primitiveColumns = _.filter(initialOrder, c => meta[c].isPrimitive);

    // Match potential derived columns for visualization
    const derivedColumns = matchDerivedColumns(meta);
    derivedColumns.forEach(col => {
      dispatch(addDerivedColumn(col));
    });

    const parsedData = {
      initialOrder, meta, primitiveColumns, countRows
    };
    dispatch(columnMetaSlice.actions.fetchSuccess(parsedData));
  } catch (err) {
    console.error(err);
    dispatch(columnMetaSlice.actions.fetchError(err.message));
  }
};