import React from 'react';
import NextLink from 'next/link';
import { MyIconButton } from '@/components/elements/Controls';
import OpenInNewOutlinedIcon from '@mui/icons-material/OpenInNewOutlined';
import { ROUTES } from '@/constants';
import path from 'path-browserify';
import { useSelector } from 'react-redux';
import { isMissing } from '@/utils/helpers';
import { useDispatch } from 'react-redux';
import { updateChildren } from '@/store/folderBrowserSlice';
import RefreshIcon from '@mui/icons-material/Refresh';
import { pathJoin } from '@/store/folderBrowserSlice';

export const OpenFolderInNewTabButton = React.memo(function OpenFolderInNewTabButton({itemId}) {
    const rootFolder = useSelector(state => state.settings.folderBrowserRootFolder);
    if (isMissing(rootFolder)) {
        return null;
    }
    return (
        <NextLink href={`/${ROUTES.FolderBrowser}?rootFolder=${pathJoin(rootFolder, itemId)}`} target="_blank">
            <MyIconButton icon={<OpenInNewOutlinedIcon />} tooltip="Open in new tab" />
        </NextLink>
    )
})

export const OpenTableInNewTabButton = React.memo(function OpenTableInNewTabButton({itemId}) {
    const rootFolder = useSelector(state => state.settings.folderBrowserRootFolder);
    const fullPath = pathJoin(rootFolder, itemId);
    const ext = path.extname(itemId);
    const format = ext.replace('.', '');
    const url = `/${ROUTES.Table}?filePath=${fullPath}&fileFormat=${format}`;
    if (isMissing(rootFolder)) {
        return null;
    } else if (['csv', 'parquet'].includes(format)) {
        return (
            <NextLink href={url} target="_blank">
                <MyIconButton icon={<OpenInNewOutlinedIcon />} tooltip="Open in new tab" />
            </NextLink>
        )
    } else {
        return null;
    }
})

export const RefreshFolderButton = React.memo(function RefreshFolderButton({itemId}) {
    const dispatch = useDispatch();
    const refreshChildren = () => {
        dispatch(updateChildren(itemId, true));
    }
    return (
        <MyIconButton icon={<RefreshIcon />} onClick={refreshChildren} tooltip="Refresh" />
    )
})
