import { useSelector } from 'react-redux';
import ClickCard from '@/components/elements/ClickCard';
import { ReadOnlySQL } from '@/components/elements/displays';
import { timeAgo } from '@/utils/time';
import { Box, Typography } from '@mui/material';
import { TextWithAddon } from '@/components/elements/displays';
import React from 'react';
import { SQLIcon } from '@/utils/icons';
import StatusChip from '@/components/elements/StatusChip';
import { BasicTable } from '@/components/elements/BasicTable';
import { ChipList } from '@/components/elements/displays';
import { MyIconButton } from '@/components/elements/Controls';

const SqlCard = React.memo(({sqlKey}) => {
    const sql = useSelector((state) => state.sql[sqlKey]);
    if (!sql) {
        return null;
    };

    const styledIcon = <MyIconButton icon={<SQLIcon/>} iconSize="medium" />;

    return (
        <Box sx={{flex: 0, height: '100%'}}>
            <ClickCard title={<TextWithAddon text={`SQL ${sqlKey}`} addon={<StatusChip status={sql.status}/>}/>} tooltip={null} trigger={styledIcon}>
                <Box sx={{flex: 1, display: 'flex', flexDirection: 'column', gap: 1, height: '100%'}}>
                    {sql.error && <Typography variant="body1" color="error">{sql.error}</Typography>}
                    <ChipList items={[
                        `Last updated: ${timeAgo(sql.lastUpdated)}`,
                        `Runtime: ${sql?.runtime?.toFixed(5)} seconds`,
                    ]} />
                    <Box sx={{flex: 1, overflow: 'auto'}}>
                        <ReadOnlySQL value={sql.query} />
                    </Box>
                    <Box sx={{flex: 1}}>
                        <BasicTable initRowData={sql.data} />
                    </Box>
                </Box>
            </ClickCard>
        </Box>
    );
});

SqlCard.displayName = 'SqlCard';

export default SqlCard;