import React, { useEffect, useRef, useState } from 'react';
import { Box, Typography, Divider } from '@mui/material';
import CellRightClickCard from '@/components/elements/CellRightClickCard';
import CellRightClickMenu from '@/components/table/rightClickMenu/CellRightClickMenu';
import { isMissing } from '@/utils/helpers';
import { useSelector } from 'react-redux';
import { TopButtons } from '@/components/table/rightClickMenu/TopButtons';

const ExpandableTextCell = React.memo(({ columnName, text, colDef }) => {

  const contentRef = useRef(null);
  const [isOverflowed, setIsOverflowed] = useState(false);
  const isJustClicked = useSelector(state => state.viewing.justClickedColumn === columnName);
  const maxRowHeight = useSelector(state => state.settings.tableMaxRowHeight);


  useEffect(() => {
    const checkOverflow = () => {
      const el = contentRef.current;
      if (el) {
        setIsOverflowed(el.scrollHeight > el.clientHeight);
      }
    };
    checkOverflow();

    // Optional: recompute on window resize if width can change
    window.addEventListener('resize', checkOverflow);
    return () => window.removeEventListener('resize', checkOverflow);
  }, [text]);


  return (
    <Box sx={{ maxHeight: maxRowHeight, overflow: 'hidden', }}>
      <Typography
        ref={contentRef}
        variant="body2"
        sx={{
          padding: 0.5,
          overflow: 'hidden',
          whiteSpace: 'normal',
          wordBreak: 'break-word',
          maxHeight: maxRowHeight,
          color: isJustClicked ? 'primary.light' : 'text.secondary',
        }}
      >
        {isMissing(text) ? '' : text}
      </Typography>

       {isOverflowed ? (
        <CellRightClickCard sx={{width: 600}}>
          <TopButtons name={columnName} value={text} colDef={colDef} />
          <Divider />
          <Typography
            variant="body2"
            sx={{padding: 1}}
          >
            {text}
          </Typography>
        </CellRightClickCard>
    ):  <CellRightClickMenu name={columnName} value={text} colDef={colDef} />}

    </Box>
  );
});

ExpandableTextCell.displayName = 'ExpandableTextCell';

export default ExpandableTextCell;