import { Box, Popover, Typography } from '@mui/material';
import { useState } from 'react';
import React from 'react';
import { MyIconButton } from '@/components/elements/Controls';
import _ from 'lodash';
import { isMissing } from '@/utils/helpers';
import { FormControlLabel, RadioGroup, Radio } from '@mui/material';
import { SX_TEXT_HOVER, SX_CARD } from '@/utils/styles';

export const IconDropdown = ({
  title,
  selectedValue, choices, onChange,
}) => {

  const [open, setOpen] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);

  if (isMissing(selectedValue)) {
    return null;
  }

  const handleOpen = (event) => {
    setAnchorEl(event.currentTarget);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
    setAnchorEl(null);
  };

  const handleClickRadio = (event) => {
    onChange(event.target.value);
    handleClose();
  }
  const iconLabel = (icon, label) => {
    return <Box display="flex" alignItems="center" gap={1}>
      { React.cloneElement(icon, {fontSize: 'small'})}
      {label}
    </Box>
  };

  const selectedIcon = _.find(choices, c => c.value === selectedValue)?.icon ;

  const popoverContent = choices.length === 1 ? (
    <Typography sx={{padding: 1}} variant="body2">It can only be {choices[0].label}</Typography>
  ) : (
    <RadioGroup
    value={selectedValue}
    onChange={handleClickRadio}
    >
    {choices.map(({icon, label, value}) => (
      <FormControlLabel
      key={value}
      value={value}
      control={<Radio size="small" />}
      label={iconLabel(icon, label)}
      sx={SX_TEXT_HOVER}
      />
    ))}
    </RadioGroup>

  )

  return (
    <Box>
      <MyIconButton onClick={handleOpen} icon={selectedIcon}/>

      <Popover
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{vertical: 'bottom', horizontal: 'left'}}
      >
        <Box sx={{...SX_CARD}}>
            <Typography variant="h6">{title}</Typography>
            {popoverContent}
        </Box>
      </Popover>
    </Box>
  );
};

