

export const columnHistogramQuery = ({filteredColumnTableExpr, histogramNumberOfBins}) => {
    return `
      WITH filtered AS (
        ${filteredColumnTableExpr}
      ), stats AS (
        SELECT 
            COUNT(*) AS cnt_all,
            COUNT_IF(value IS NULL) AS cnt_null,
            MIN(value) AS raw_min, 
            MAX(value) AS raw_max,
            ${histogramNumberOfBins} AS raw_bins,
            CASE WHEN raw_min = raw_max THEN 1 ELSE (raw_max::DOUBLE - raw_min::DOUBLE) / raw_bins END AS raw_step,
            -FLOOR(LOG(raw_step))::INT AS round_to,
            ROUND(raw_step, round_to) AS nice_step,
            ROUND(FLOOR(raw_min / nice_step) * nice_step, round_to) AS nice_min,
            ROUND(CEIL(raw_max / nice_step) * nice_step, round_to) AS nice_max,
            ((nice_max - nice_min) / nice_step) + 1 AS nice_bins
        FROM filtered
        ), bins AS (
        SELECT
          FLOOR((value - stats.nice_min) / stats.nice_step) AS bin_idx,
          COUNT(*) AS cnt
        FROM
          filtered, stats
        GROUP BY 1 ORDER BY 1
        ), grouped_bins AS (
          SELECT ARRAY_AGG(STRUCT_PACK(binIdx:=bin_idx, cnt:=cnt)) AS cnt_values
          FROM bins
        )
        SELECT
          STRUCT_PACK(
            min := stats.nice_min,
            max := stats.nice_max,
            count := stats.nice_bins,
            step := stats.nice_step,
            round_to := stats.round_to
          ) AS bin,
          STRUCT_PACK(
            min := stats.raw_min,
            max := stats.raw_max
          ) AS range,
          cnt_values,
          stats.cnt_all,
          stats.cnt_null,
          stats.cnt_all - stats.cnt_null AS cnt_not_null
        FROM grouped_bins, stats
    `;
}

export const columnCategoryQuery = ({filteredColumnTableExpr, thresholdCategorical}) => {
    return `
      WITH filtered AS (
        ${filteredColumnTableExpr}
      ), stats AS (
        SELECT 
            COUNT(*) AS cnt_all,
            COUNT_IF(value IS NULL) AS cnt_null,
            MIN(value) AS min, 
            MAX(value) AS max
        FROM filtered
        ), bins AS (
        SELECT value, COUNT(*) AS cnt
        FROM filtered WHERE value IS NOT NULL
        GROUP BY value ORDER BY cnt DESC
        LIMIT ${thresholdCategorical}
        ), grouped_bins AS (
          SELECT ARRAY_AGG(STRUCT_PACK(value:=value, cnt:=cnt)) AS cnt_values
          FROM bins
        ) SELECT
          STRUCT_PACK(
            min := stats.min,
            max := stats.max
          ) AS range,
          cnt_values,
          stats.cnt_all,
          stats.cnt_null,
          stats.cnt_all - stats.cnt_null AS cnt_not_null
        FROM grouped_bins, stats
      `;
}