import React from 'react';
import { Box, Breadcrumbs, CircularProgress } from '@mui/material';
import { RichTreeView } from '@mui/x-tree-view/RichTreeView';
import { useTreeViewApiRef } from '@mui/x-tree-view/hooks';
import { useSelector, useDispatch } from 'react-redux';
import { updateChildren, locateItem, pathBasename, pathDirname } from '@/store/folderBrowserSlice';
import { useEffect } from 'react';
import { setViewingItemId, setExpandedItems } from '@/store/folderBrowserSlice';
import { CustomTreeItem } from '@/components/folderBrowser/CustomTreeItem';
import path from 'path';
import { SX_TEXT_HOVER, SX_WIDTH_100, SX_BORDER } from '@/utils/styles';
import { BreadCrumbLink } from '@/components/folderBrowser/BreadCumbsNav';
import { FolderNavControls } from '@/components/folderBrowser/FolderNavControls';
import { updateSettings } from '@/store/settingsSlice';
import { useInitSettings } from '@/hooks/useApiData';
import { isMissing } from '@/utils/helpers';

const FolderNav = React.memo(function FolderNav({}) {
    const dispatch = useDispatch();
    useInitSettings();
    const expandedItems = useSelector(state => state.folderBrowser.expandedItems);
    const apiRef = useTreeViewApiRef();
    const rootFolder = useSelector(state => state.settings.folderBrowserRootFolder);
    const treeData = useSelector(state => state.folderBrowser.treeData);
    const viewingItemId = useSelector(state => state.folderBrowser.viewingItemId);

    useEffect(() => {
        if (viewingItemId && apiRef.current) {
            //apiRef.current.setItemExpansion({itemId: viewingItemId, shouldBeExpanded: true});
            apiRef.current.setItemSelection({itemId: viewingItemId, keepExistingSelection: false, shouldBeSelected: true});
        }
    }, [viewingItemId, apiRef]);

    if (isMissing(rootFolder)) {
        return <CircularProgress/>;
    }

    const rootParent = pathDirname(rootFolder);


    const goToRootParent = () => {
        dispatch(updateSettings({folderBrowserRootFolder: rootParent}));
        dispatch(updateChildren(''));
        dispatch(setViewingItemId(''));
        dispatch(setExpandedItems([]));
    }

    const viewItem = (itemId) => {
        const item = locateItem(treeData, itemId);
        if (item.isDir) {
            dispatch(updateChildren(itemId));
        }
        dispatch(setViewingItemId(itemId));
    }

    const onItemClick = (event, itemId) => {
        viewItem(itemId);
    }

    const onItemExpansionToggle = (event, itemId, isExpanded) => {
        if (isExpanded) {
            dispatch(updateChildren(itemId));
        }
        if (event) {
            event.stopPropagation();
        }
    }
    const onItemSelectionToggle = (event, itemId, isSelected) => {
        if (isSelected) {
            viewItem(itemId);
        }

      };

    const onItemFocus = (event, itemId) => {
        viewItem(itemId);
    }

    return (
        <Box sx={{
            display: 'flex', flexDirection: 'column',
            height: '100%', gap: 1,
            backgroundColor: 'background.paper'
        }}>
            <FolderNavControls />
            <Box sx={{
                flex: 0, display: 'flex', flexDirection: 'row', alignItems: 'center', gap: 1, p: 1,
                mx: 1, ...SX_BORDER,
                backgroundColor: 'background.card',

                }}>

                <Breadcrumbs sx={{...SX_TEXT_HOVER, ...SX_WIDTH_100}}>
                    {rootParent !== '.' && (
                        <BreadCrumbLink onClick={goToRootParent} label={pathBasename(rootParent)} />
                    )}

                    <BreadCrumbLink onClick={() => dispatch(setViewingItemId(''))} label={pathBasename(rootFolder)} />
                </Breadcrumbs>

            </Box>

            <Box sx={{
                flex: 1, overflow: 'auto', backgroundColor: 'background.card',
                m: 1, borderRadius: 1, border: '1px solid', borderColor: 'divider'
            }}>

                <RichTreeView
                    apiRef={apiRef}
                    items={treeData}
                    expandedItems={expandedItems}
                    onExpandedItemsChange={(event, nodeIds) => {
                        dispatch(setExpandedItems(nodeIds));
                    }}
                    onItemClick={onItemClick}
                    expansionTrigger="iconContainer"
                    onItemExpansionToggle={onItemExpansionToggle}
                    onItemSelectionToggle={onItemSelectionToggle}
                    onItemFocus={onItemFocus}
                    slots={{ item: CustomTreeItem }}
                />
            </Box>

        </Box>
    );
});


export default FolderNav;