import { Box } from '@mui/material';
import FiltersArrow from './FiltersArrow';
import useFilters from '@/hooks/useFilters';
import { stringToColor } from '@/utils/colors';
import React from 'react';


export const FilteredCount = React.memo(({countBaseline, countFiltered, label, addColor = false, isPercentage = false}) => {
    const {backgroundColor} = stringToColor(label);
    const { hasFilters } = useFilters();
    const colorBox = addColor ? <Box sx={{width: 15, height: 15, backgroundColor}} /> : null;
    const showArrow = hasFilters && countBaseline !== countFiltered;

    return (
    <Box sx={{display: 'flex', alignItems: 'center', gap: 0.5}}>
        {colorBox}
        <Box>{label}: </Box>
        <Box sx={{color: 'text.secondary', display: 'flex', alignItems: 'center', gap: 0.5, mr: 1}}>
            {showArrow && <Box>{countBaseline.toLocaleString()}{isPercentage ? '%' : ''}</Box>}
            {showArrow && <Box><FiltersArrow /></Box>}
            <Box>{countFiltered?.toLocaleString()}{isPercentage ? '%' : ''}</Box>
        </Box>
    </Box>
    );

});

FilteredCount.displayName = 'FilteredCount';