import { useSelector } from 'react-redux';
import _ from 'lodash';
import { createSelector } from '@reduxjs/toolkit';
import { toSqlCondition } from '@/utils/sql/sql';

const useFilters = () => {
    const parsedState = useSelector(createSelector([
        (state) => state.agColDefs.baselineFilters,
        (state) => state.agColDefs.filters,
        (state) => state.agColDefs.extraFilter || '',
    ], (baselineFilters, filters, extraFilter) => {
        const baselineFiltersSql = toSqlCondition(baselineFilters) || 'TRUE';
        const filtersSql = toSqlCondition(filters) || 'TRUE';
        const allFiltersSql = `(${baselineFiltersSql}) AND (${filtersSql})`;
        return {
            baselineFilters,
            filters,
            extraFilter,
            hasFilters: !_.isEmpty(filters) || extraFilter !== '',
            baselineFiltersSql,
            filtersSql,
            allFiltersSql,
        };
    }));


    return parsedState;
};

export default useFilters;