
import { Typography } from '@mui/material';
import { useSelector } from 'react-redux';
import CellRightClickCard from '@/components/table/rightClickMenu/CellRightClickCard';
import { Box } from '@mui/material';
import { JsonBox } from '@/components/elements/JsonBox';
import { TopButtons } from '@/components/table/rightClickMenu/TopButtons';
import React from 'react';
import { Divider } from '@mui/material';


export const JsonBoxCell = React.memo(function JsonBoxCell({ name, value, colDef }) {
  const obj = value;
  const tableMaxRowHeight = useSelector(state => state.settings.tableMaxRowHeight);
  if (!obj) {
    return <Typography variant="body2">{obj}</Typography>;
  }


  return (
    <Box>
      <Typography variant="body2" color="inherit" sx={{
        padding: 0.5,
        overflow: 'hidden', maxHeight: tableMaxRowHeight, whiteSpace: 'normal',
      }}>
        {JSON.stringify(obj)}
      </Typography>

      <CellRightClickCard sx={{width: 400}}>
        <TopButtons name={name} value={value} colDef={colDef} showCopy={false} />
        <Divider />
        <JsonBox obj={obj} />
      </CellRightClickCard>
    </Box>
  );
});


