import _ from 'lodash';
import { useDispatch } from 'react-redux';
import { updateColumnPreference } from '@/store/columnPreferenceSlice';
import CircleOutlinedIcon from '@mui/icons-material/CircleOutlined';
import { setEqual } from '@/utils/helpers';
import { baselineColumnStatsSlice, filteredColumnStatsSlice } from '@/store/dataSlice';
import { IconDropdown } from '@/components/elements/IconDropdown';
import React from 'react';
import { useSingleColumnMeta } from '@/hooks/useApiData';
import { useSingleColumnPreference } from '@/hooks/useColumnPreference';
import { useSelector } from 'react-redux';
import { isMissing } from '@/utils/helpers';
import { useCardinality } from '@/hooks/useCardinality';
import { CircularProgress } from '@mui/material';
import { RENDER_TYPES } from '@/constants';

import { ICONS_FOR_RENDER_TYPE } from '@/configs/iconsForRenderType';
import useSetting from '@/hooks/useSetting';


export const RenderTypeIcon = React.memo(function RenderTypeIcon({ columnName }) {
    const dispatch = useDispatch();
    const meta = useSingleColumnMeta(columnName);
    const pref = useSingleColumnPreference(columnName);
    const {data: cardinalityData} = useCardinality(columnName) || {};
    const { value: baseFontSize } = useSetting('baseFontSize');
    const thresholdCategorical = useSelector(state => state.settings.thresholdCategorical);
    if (isMissing(pref) || isMissing(meta) || isMissing(cardinalityData)) {
        return <CircularProgress size={baseFontSize}/>;
    }
    const renderType = pref.renderType;
    const canBeCategorical = cardinalityData?.cntD <= thresholdCategorical;
    const getCandidates = () => {
        if (meta.isDerived) {
            return [pref.renderType];
        } else if (meta.isNumeric) {
            return [
                RENDER_TYPES.NUMBER,
                ...(canBeCategorical ? [RENDER_TYPES.CATEGORY] : []),
            ];
        } else if (meta.isString) {
            return [
                RENDER_TYPES.TEXT, RENDER_TYPES.IMAGE_URL, RENDER_TYPES.VIDEO_URL, RENDER_TYPES.JSON,
                ...(canBeCategorical ? [RENDER_TYPES.CATEGORY] : []),
            ];
        } else if (meta.isBoolean) {
            return [RENDER_TYPES.CATEGORY];
        } else if (meta.isDatetime) {
            return [RENDER_TYPES.TEXT];
        } else if (!meta.isPrimitive) {
            return [RENDER_TYPES.JSON];
        } else {
            return Object.keys(ICONS_FOR_RENDER_TYPE);
        }
    };



    const choices = _.map(getCandidates(), candidate => ({
        icon: ICONS_FOR_RENDER_TYPE[candidate] || <CircleOutlinedIcon/>,
        label: candidate,
        value: candidate
    }));


    const setRenderType = (newRenderType) => {
        dispatch(updateColumnPreference({name: columnName, renderType: newRenderType}));

        // Refresh stats if render type is changed between Category and Number
        if (setEqual([renderType, newRenderType], [RENDER_TYPES.CATEGORY, RENDER_TYPES.NUMBER])) {
            dispatch(baselineColumnStatsSlice.actions.setShouldRefresh({ name: columnName }));
            dispatch(filteredColumnStatsSlice.actions.setShouldRefresh({ name: columnName }));
        }

    }


    return (
        <IconDropdown
            title="Change render type"
            selectedValue={renderType}
            choices={choices}
            onChange={setRenderType}
        />
    );

});


export default RenderTypeIcon;

