import { Treemap, ResponsiveContainer, Tooltip } from 'recharts';
import { map, filter } from 'lodash';
import { Box } from '@mui/material';
import { truncateText } from '@/utils/StringsUtils';
import useMUITheme from '@/hooks/useMUITheme';
import React from 'react';

const MyTreeMap = React.memo(({ columnStats, setClickedId, size=100 }) => {
    const muiTheme = useMUITheme();

    if (!columnStats || columnStats.cnt_values.length === 0) {
        return <Box sx={{height: size, border: '1px solid', borderColor: 'divider', display: 'flex', alignItems: 'center', justifyContent: 'center'}}>no data</Box>;
    }
    const { cnt_values, cnt_all } = columnStats;
    const chartData = cnt_values;
    const filteredData = map(filter(chartData, (item) => item.cnt > 0), ({value, cnt}) => ({name: value, value: cnt}));


    const CustomizedContent = (props) => {
        const { x, y, width, height, index, name } = props;
        return (
          <g onClick={() => setClickedId ? setClickedId(name) : null}>
            <rect
              x={x}
              y={y}
              width={width}
              height={height}
              style={{
                fill: muiTheme.palette.plotFill,
                stroke: muiTheme.palette.text.primary,
                strokeWidth: 1,
                cursor: 'pointer',
              }}
            />
            {height > 20 && width > 30 && (
                <text
                    x={x + width / 2}
                    y={y + height / 2}
                    textAnchor="middle"
                    dominantBaseline="central"
                    fill={muiTheme.palette.text.primary}
                    stroke={muiTheme.palette.text.primary}
                    strokeWidth={1}
                    fontSize={muiTheme.typography.fontSize}
                    style={{
                        maxWidth: width,
                        overflow: 'hidden',
                        whiteSpace: 'nowrap',
                        textOverflow: 'ellipsis',
                    }}
            >
                {truncateText(name, width)}
            </text>
            )}
          </g>
        );
      };

    const TreeMapTooltip = ({ active, payload }) => {
        if (active && payload && payload.length) {
            const value = payload[0].payload.name;
            const cnt = payload[0].payload.value;
            const percentage = cnt_all > 0 ? ((cnt / cnt_all) * 100).toFixed(2) : 0;

            return (
                <Box sx={{
                    p: 1,
                    bgcolor: 'background.card',
                    color: 'text.secondary',
                    borderRadius: 1,
                    border: '1px solid',
                    borderColor: muiTheme.palette.borderColor,
                    fontSize: muiTheme.typography.fontSize,
                }}>
                    <Box sx={{ mb: 1, fontWeight: 'bold'}}>{`${value}`}</Box>
                    <Box>cnt: {cnt.toLocaleString()}</Box>
                    <Box>pct: {percentage}%</Box>
                </Box>
            );
        }
        return null;
    };

    return (
        <ResponsiveContainer minWidth={24} minHeight={24} width='100%' height={size} sx={{overflow: 'hidden'}}>
            <Treemap
                data={filteredData}
                stroke="#fff"
                fill="#8884d8"
                isAnimationActive={false}
                content={<CustomizedContent />}
            >

                {size >= 100 && <Tooltip content={<TreeMapTooltip />} isAnimationActive={false}/>}
            </Treemap>

        </ResponsiveContainer>
    );
});

MyTreeMap.displayName = 'MyTreeMap';

export default MyTreeMap;

