'use client';
import { Box, Divider } from '@mui/material';
import { proxyedUrl } from '@/utils/urls';
import React from 'react';
import useSetting from '@/hooks/useSetting';
import { ImageBox } from '@/components/elements/ImageBox';
import CellRightClickCard from '@/components/table/rightClickMenu/CellRightClickCard';
import { TopButtons } from '@/components/table/rightClickMenu/TopButtons';

export const ImageBoxCell = React.memo(function ImageBoxCell({name, value, colDef}) {
  const url = value;
  const {value: size } = useSetting('tableMaxRowHeight');
  const largeSize = 500;
  const proxyUrl = proxyedUrl(url);

  return (
    <Box>
      <img src={proxyUrl} alt={name} style={{objectFit: 'cover', width: size, height: size}} />
      <CellRightClickCard>
        <TopButtons name={name} value={value} colDef={colDef} />
        <Divider />
        <ImageBox url={url} size={largeSize} cropSquare={false} showUrl={true} />
      </CellRightClickCard>
    </Box>
  );
});
