import React from 'react';
import { Box } from '@mui/material';
import { alpha } from '@mui/material/styles';
import useMUITheme from '@/hooks/useMUITheme';

export const FloatingRightContainer = React.memo(function FloatingRightContainer({children, sx}) {
    const muiTheme = useMUITheme();
    return (
        <Box sx={{
            position: 'absolute', right: 0, top: 0, display: 'flex', flexDirection: 'row', gap: 0,
            backgroundColor: alpha(muiTheme.palette.background.card, 0.5),
            height: '100%',
            ...sx,
        }}>
            {children}
        </Box>
    )
})

export const RowBoxes = React.memo(function RowBoxes({children}) {
    const validChildren = React.Children.toArray(children).filter(child => {
      if (typeof child === 'string') {
        return child.trim().length > 0; // ignore pure whitespace strings
      } else {
        return true;
      }
    });

    return (
      <Box sx={{
        flex: 1, width: '100%', height: '100%', minWidth: 0,
        display: 'flex', flexDirection: 'row', gap: 1, alignItems: 'center'
      }}>
        {validChildren.map((child, index) => (
          <Box key={index} sx={{flex: 1, minWidth: 0}}>
            {child}
          </Box>
        ))}
      </Box>
    );
  });


