'use client';

import { useDispatch } from 'react-redux';
import { MenuList, Divider } from '@mui/material';
import React from 'react';
import GridViewOutlinedIcon from '@mui/icons-material/GridViewOutlined';
import { updateSettings } from '@/store/settingsSlice';
import { removeColumnFilterAndRefreshData } from '@/store/agColDefsSlice';
import FilterAltOffOutlinedIcon from '@mui/icons-material/FilterAltOffOutlined';
import { isMissing } from '@/utils/helpers';
import { useSelector } from 'react-redux';
import CellRightClickCard from '@/components/elements/CellRightClickCard';
import { MenuRow } from '@/components/table/rightClickMenu/MenuRow';
import { TopButtons } from '@/components/table/rightClickMenu/TopButtons';



const CellRightClickMenu = React.memo(function CellRightClickMenu({name, value, colDef}) {
  const dispatch = useDispatch();

  const hasFilter = !isMissing(useSelector(state => state.agColDefs.filters[name]));


  const handleShowInGalleryCaption = () => {
    dispatch(updateSettings({galleryCaptionColumn: name}));

  };

  const handleClearFilters = () => {
    removeColumnFilterAndRefreshData(dispatch, name);

  };


  return (
    <CellRightClickCard sx={{minWidth: 100}}>

        <TopButtons name={name} value={value} colDef={colDef} />

        <Divider />

        <MenuList sx={{display: 'flex', flexDirection: 'column', gap: 0.5}}>
            <MenuRow icon={<GridViewOutlinedIcon />} text="Show in Gallery caption" onClick={handleShowInGalleryCaption} />
            {hasFilter && <MenuRow icon={<FilterAltOffOutlinedIcon />} text="Clear filters" onClick={handleClearFilters} />}
        </MenuList>




    </CellRightClickCard>

    )

});


export default CellRightClickMenu;