import { isMissing } from '@/utils/helpers';


export function numberValueFormatter (params) {
    return isMissing(params.value) ? '' : `${params.value.toLocaleString()}`;
}

export function percentValueFormatter (params) {
    return isMissing(params.value) ? '' : `${params.value.toLocaleString()}%`;
}

export const COL_DEF_NUMBER = (muiTheme) => ({
    valueFormatter: numberValueFormatter,
    cellStyle: {
        textAlign: 'right',
        fontFamily: 'monospace',
        fontSize: muiTheme.typography.fontSize,
    }
});

export const COL_DEF_PERCENT = (muiTheme) => ({
    valueFormatter: percentValueFormatter,
    cellStyle: {
        textAlign: 'right',
        fontFamily: 'monospace',
        fontSize: muiTheme.typography.fontSize,
    }
});


export const buildProgressBarRenderer = ({min, max, bgColor='#4caf50', isPercent=false, muiTheme}) => {
    const ProgressBarRenderer = (params) => {
      if (isMissing(params.value)) return null;
      const value = params.value;
      const percentage = 100 * Math.max(0, Math.min(1, (value - min) / (max - min)));
      const valStr = value.toLocaleString();
      return (
        <div style={{
          position: 'relative',
          height: '100%',
          width: '100%',
          backgroundColor: 'transparent',
          borderColor: muiTheme.palette.borderColor,
          borderWidth: 1,
          borderStyle: 'solid',
        }}>
          <div style={{
            position: 'absolute',
            top: 0,
            bottom: 0,
            left: 0,
            width: `${percentage}%`,
            backgroundColor: bgColor,
            opacity: 0.6,
            borderRadius: 2,
          }} />
          <div style={{
            position: 'relative',
            paddingLeft: 4,
            paddingRight: 4,
            fontWeight: 500,
            fontSize: muiTheme.typography.fontSize,
            fontFamily: 'monospace',
            color: 'inherit',
            zIndex: 1,
          }}>
            {isPercent ? `${valStr}%` : valStr}
          </div>
        </div>
      );
    };
    return ProgressBarRenderer;
};





