import { useDispatch } from 'react-redux';
import { updateSettings } from '@/store/settingsSlice';
import { isMissing } from '@/utils/helpers';
import React from 'react';
import { MyCheckbox } from '@/components/elements/Controls';
import { CircularProgress } from '@mui/material';
import useSetting from '@/hooks/useSetting';


const CheckboxInSettings = React.memo(({settingKey, extraChange=null, shortLabel=null}) => {
    const dispatch = useDispatch();
    const {schema, initValue, value} = useSetting(settingKey);

    if (isMissing(initValue)) {
        return <CircularProgress />;
    }


    const setValue = (newValue) => {
        dispatch(updateSettings({[settingKey]: newValue}));
        if (extraChange) {
            extraChange(newValue);
        }
    }

    return (
        <MyCheckbox label={shortLabel || schema.description} checked={value} onChange={setValue} />
    );
});

CheckboxInSettings.displayName = 'CheckboxInSettings';

export default CheckboxInSettings;
