import React from 'react';
import { Box } from '@mui/material';
import path from 'path-browserify';
import { imageExtensions } from '@/utils/typesUtils';
import { useSelector, useDispatch } from 'react-redux';
import ViewImage from '@/components/folderBrowser/fileViewers/ViewImage';
import TextContentViewer from '@/components/folderBrowser/fileViewers/TextContentViewer';
import ParquetPreview from '@/components/folderBrowser/fileViewers/ParquetPreview';
import { TextSegments } from '@/components/elements/displays';
import BreadCrumbsNav from '@/components/folderBrowser/BreadCumbsNav';
import CsvPreview from '@/components/folderBrowser/fileViewers/CsvPreview';
import { pathJoin } from '@/store/folderBrowserSlice';

const FileViewer = React.memo(function FileViewer({viewingItem}) {
    const dispatch = useDispatch();
    const { id, label } = viewingItem;
    const rootFolder = useSelector(state => state.settings.folderBrowserRootFolder);

    const fullPath = pathJoin(rootFolder, id);

    const ext = path.extname(id);


    const mainView = () => {

        if (imageExtensions.includes(ext)) {
            return <ViewImage fullPath={fullPath} />;
        } else if (['.json', '.yaml', '.yml', '.py', '.sql', '.md'].includes(ext)) {
            return <TextContentViewer fullPath={fullPath} />;
        } else if (ext === '.parquet') {
            return <ParquetPreview fullPath={fullPath} />;
        } else if (ext === '.csv') {
            return <CsvPreview fullPath={fullPath} />;
        } else {
            return <TextSegments segments={[
                {text: 'I do not know how to preview the file with extension'},
                {text: ext, sx: {fontFamily: 'monospace', color: 'warning.main'}},
            ]} />
        }
    }

    return (
    <Box sx={{
        width: '100%', height: '100%', display: 'flex', flexDirection: 'column',
        padding: 2, gap: 2, alignItems: 'left',
        backgroundColor: 'background.paper'
    }}>
        <Box sx={{display: 'flex', flexDirection: 'row', gap: 2, alignItems: 'center'}}>
            <BreadCrumbsNav id={id} isDir={false} />
            <Box sx={{flex: 1}}></Box>

        </Box>

        <Box sx={{height: '100%', width: '100%', overflow: 'auto'}}>
            {mainView()}
        </Box>
    </Box>
    );
});

export default FileViewer;