import { SvgIcon } from '@mui/material';
import React from 'react';

const TextIcon = React.forwardRef(({text, color = 'inherit', fontSize = 'inherit', ...props}, ref) => {
    return (
        <SvgIcon {...props} viewBox="0 0 64 64" color={color} fontSize={fontSize} ref={ref}>
            <text
                x="50%"
                y="50%"
                textAnchor="middle"
                dominantBaseline="middle"
                fontSize="36"
                fontFamily="monospace"
                fill="currentColor"
            >
            {text}
            </text>
            <line x1="0" y1="64" x2="64" y2="64" stroke="currentColor" strokeWidth="12"/>
        </SvgIcon>
    );
});

TextIcon.displayName = 'TextIcon';

export const SQLIcon = React.forwardRef(({...props}, ref) => {
    return <TextIcon text="SQL" {...props} ref={ref} />;
});

SQLIcon.displayName = 'SQLIcon';

export const JSONIcon = React.forwardRef(({...props}, ref) => {
    return <TextIcon text="JSON" {...props} ref={ref} />;
});

JSONIcon.displayName = 'JSONIcon';


