import { Chip } from '@mui/material';
import React from 'react';
import { isMissing } from '@/utils/helpers';

const StatusChip = React.memo(function StatusChip({status}) {
    if (isMissing(status)) {
        return null;
    } else {
        const color = {
            running: 'warning',
            success: 'success',
            error: 'error',
        }[status] || 'default';

        return <Chip label={status} size="small" color={color} variant="outlined"/>
    }
});


export default StatusChip;