import {
  IconButton,
  ButtonGroup, FormControlLabel,
  Checkbox,
  Typography,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Slider,
  Box,
  Button, TextField,
  Autocomplete
} from '@mui/material';
import { zIndex } from '@/utils/zIndex';
import React from 'react';
import { MyTooltip } from '@/components/elements/displays';
import { SX_TEXT_HOVER } from '@/utils/styles';
import CircleOutlinedIcon from '@mui/icons-material/CircleOutlined';
import ContentPasteOutlinedIcon from '@mui/icons-material/ContentPasteOutlined';
import { useState } from 'react';

export const TooltipButton = React.memo(function TooltipButton({icon, title, onClick, placement="top"}) {
  return (
    <MyTooltip title={title} placement={placement}>
      <IconButton size="small" onClick={onClick}>{icon}</IconButton>
    </MyTooltip>
  );
});

export const BooleanIconButton = React.memo(function BooleanIconButton({
  iconOn, iconOff, value, onClick,
  tooltipOn=null, tooltipOff=null
}) {

  return (
    <MyIconButton
      icon={React.cloneElement((value ? iconOn : iconOff), {fontSize: 'small'})}
      onClick={onClick}
      tooltip={value ? tooltipOn : tooltipOff}
      sx={{
        color: value ? 'primary.main' : 'text.secondary',
        '&:hover': {
          color: value ? 'primary.light' : 'text.primary'
        }
      }}
      >
      </MyIconButton>
  );
});


export const MyIconButton = React.memo(function MyIconButton({icon, onClick=null, disabled=false, iconSize='small', tooltip=null, sx={}}) {
  const styledIcon = React.cloneElement(icon || <CircleOutlinedIcon/>, {fontSize: iconSize});
  const button = (
    <IconButton size="small" onClick={onClick} sx={{...SX_TEXT_HOVER, ...sx}} disabled={disabled}>
      {styledIcon}
    </IconButton>
  );
  if (tooltip) {
    return <MyTooltip title={tooltip}>{button}</MyTooltip>
  } else {
    return button;
  }
});

export const MyButtonGroup = React.memo(function MyButtonGroup({children}) {
  return (
    <ButtonGroup variant="outlined" size="small" sx={{ p: 1, border: 1, borderColor: 'divider' }}>
        {children}
    </ButtonGroup>
  );
});

export const MyCheckbox = React.memo(function MyCheckbox({label, checked, onChange, disabled=false, maxWidth=200}) {
    const cb = <Checkbox disabled={disabled} checked={checked} size="small" onChange={(e) => onChange(e.target.checked)}/>;
    const lb = (
    <Typography
      variant="body2"
      noWrap
      sx={{
        maxWidth: 200,
        ...(disabled ? {color: 'text.tertiary'} : SX_TEXT_HOVER),
        cursor: disabled ? 'not-allowed' : 'pointer',
        userSelect: 'none',
        WebkitUserSelect: 'none',
        MozUserSelect: 'none',
        msUserSelect: 'none',
      }}
      >
        {label}
      </Typography>
  );
    return <FormControlLabel control={cb} label={lb} sx={{width: '100%', maxWidth}}/>;
});



export const MyDropdown = React.memo(function MyDropdown({options, value, onChange=null, label, width=null, myKey=null}) {
    return  <FormControl size="small" sx={{ width: width ? width : '100%'}}>
    <InputLabel>{label}</InputLabel>
    <Select
      value={value}
      onChange={(e) => onChange(e.target.value)}
      label={label}
      MenuProps={{
        sx: {
          zIndex: zIndex.alwaysOnTop
        }
      }}
    >
      {options.map(option => (
        <MenuItem key={myKey ? `${myKey}-${option}` : option} value={option}>{option}</MenuItem>
      ))}
    </Select>
  </FormControl>;
});

export const MyTypeaheadDropdown = React.memo(function MyTypeaheadDropdown({options, value, onChange, label, multiple = false}) {
  return (
    <Autocomplete
        options={options}
        value={value}
        size="small"
        sx={{width: '100%'}}
        multiple={multiple}
        onChange={(event, newValue) => onChange(newValue)}
        renderInput={(params) => <TextField {...params} label={label} size="small" />}
      />
  );
});


export const MySlider = React.memo(function MySlider({label, value, onChange, min, max, step=1, width=200}) {
  return <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mt: 2, flex:1, width: '100%' }}>
  <Typography variant="body2" sx={{width: 100}}>{label}</Typography>
    <Slider
        value={value}
        onChange={(e, newValue) => onChange(newValue)}
        min={min}
        max={max}
        step={step}
        size="small"
        valueLabelDisplay="auto"
        sx={{ width: width }}
    />
  </Box>

});


export const MyTextField = React.memo(function MyTextField({
  label, value, onValueChange, type='text', monospace=false, sx={}, color='text.primary'
 }) {
  const onChange = (event) => {
    onValueChange(event.target.value);
  }
  return <TextField
            size='small'
            type={type}
            label={label}
            variant='outlined'
            value={value}
            onChange={onChange}
            slotProps={{input: {spellCheck: !monospace}}}
            sx={{width: '100%', input: {fontFamily: monospace ? 'monospace' : 'inherit', color}, ...sx}}
            />;
});

export const MyNumberField = React.memo(function MyNumberField({label, value, onValueChange }) {
  return <MyTextField label={label} value={value} onValueChange={onValueChange} type='number' />;
});

export const MyButton = React.memo(function MyButton({
  children, onClick, color='primary', variant='contained', size='small',
  startIcon=null,
}) {
  return <Button
            variant={variant}
            size={size}
            color={color}
            onClick={onClick}
            startIcon={startIcon}
            sx={{textTransform: 'none', margin: 0.5, px: 1, py: 0.5, gap: 0.5, whiteSpace: 'nowrap'}}
            >
          {children}
        </Button>;
});

export const CopyToClipboardButton = React.memo(function CopyToClipboardButton({value}) {
  const [copied, setCopied] = useState(false);
  const handleCopy = () => {
    navigator.clipboard.writeText(value);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  }
  return (
    <MyIconButton
      icon={<ContentPasteOutlinedIcon />}
      onClick={handleCopy}
      tooltip={copied ? 'Copied!' : 'Copy to clipboard'}
      iconSize="small"
    />
  );
});