import React from 'react';
import { useSelector } from 'react-redux';
import { createSelector } from '@reduxjs/toolkit';
import { locateItem } from '@/store/folderBrowserSlice';
import { isMissing } from '@/utils/helpers';
import FolderViewer from '@/components/folderBrowser/folderViewers/FolderViewer';
import FileViewer from '@/components/folderBrowser/fileViewers/FileViewer';
import { useInitSettings } from '@/hooks/useApiData';
import { pathBasename } from '@/store/folderBrowserSlice';


const FolderItemPreview = React.memo(function FolderItemPreview() {
    useInitSettings();
    const rootFolder = useSelector(state => state.settings.folderBrowserRootFolder);
    const viewingItem = useSelector(createSelector([
        state => state.folderBrowser.viewingItemId,
        state => state.folderBrowser.treeData,
    ], (viewingItemId, treeData) => {
        if (isMissing(rootFolder)) {
            return null;
        } else if (isMissing(viewingItemId) || isMissing(treeData) || viewingItemId === '') {
            return {id: '', isDir: true, label: pathBasename(rootFolder), children: treeData};
        } else {
            return locateItem(treeData, viewingItemId);
        }
    }));


    if (!viewingItem || isMissing(rootFolder)) {
        return null;
    }

    const { isDir } = viewingItem || {};


    if (isDir) {
        return <FolderViewer viewingItem={viewingItem} />;
    } else {
        return <FileViewer viewingItem={viewingItem} />;
    }

});

export default FolderItemPreview;