import { Box } from '@mui/material';
import React from 'react';
import { MyButton } from '@/components/elements/Controls';
import ColumnFilterIcon from './ColumnFilterIcon';
import { useFilteredColumnStats } from '@/hooks/useColumnStats';
import SqlCard from '@/components/elements/SqlCard';

const FilterCardBottomButtons = React.memo(({
    columnName, cancelChanges, applyFilters
}) => {

    const { sqlKey } = useFilteredColumnStats(columnName);

    return (
        <Box sx={{display: 'flex', gap: 2, justifyContent: 'flex-end', alignItems: 'center', paddingTop: 1}}>


            <ColumnFilterIcon columnName={columnName}/>
            <SqlCard sqlKey={sqlKey} />

            <Box sx={{flex: 1}}></Box>

            <MyButton onClick={cancelChanges} color="tertiary">Cancel changes</MyButton>
            <MyButton onClick={applyFilters}>Apply filters</MyButton>

        </Box>
    )
});

FilterCardBottomButtons.displayName = 'FilterCardBottomButtons';

export default FilterCardBottomButtons;