import { Chip, Box, Typography, Tooltip, List, ListItem, ListItemText } from '@mui/material';
import { sql } from '@codemirror/lang-sql';
import CodeMirror from '@uiw/react-codemirror';
import React from 'react';
import { oneDark } from '@codemirror/theme-one-dark';
import { useSelector } from 'react-redux';
import NextLink from 'next/link';
import { proxyedUrl } from '@/utils/urls';
import ContentPasteOutlinedIcon from '@mui/icons-material/ContentPasteOutlined';

export const ChipList = React.memo(function ChipList({items}) {
  return (
  <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1, alignItems: 'center' }}>
      {(items || []).map((item, index) => {
        return (
            <Chip
              key={index}
              label={item}
              size="small"
              sx={{
                border: '1px solid',
                borderColor: 'divider',
                '&:hover': {
                  borderColor: 'text.secondary',
                },
              }}
            />

        );
      })}
  </Box>
  );
});

export const MyTooltip = React.memo(function MyTooltip({title, placement="top", children}) {
  const content = (
    <Box>
      <Typography variant="body2" sx={{padding: 1,}}>{title}</Typography>
    </Box>
  );
  return (
    <Tooltip title={content} arrow placement={placement} enterDelay={300}>
      {children}
    </Tooltip>
  );
});

export const SectionHeader = React.memo(function SectionHeader({title}) {
    return (
    <Typography variant="h6" sx={{mt: 2}} gutterBottom>
        {title}
    </Typography>
    )
});

export const TextSegments = React.memo(function TextSegments({variant='body2', segments}) {
  return (
    <Box sx={{display: 'flex', flexDirection: 'row', gap: 1, alignItems: 'center'}}>
      {segments.map((seg, index) => (
        <Typography key={index} variant={variant} sx={seg.sx}>{seg.text}</Typography>
      ))}
    </Box>
  )
});

export const AGGridResponsiveContainer = React.memo(function AGGridResponsiveContainer({minWidth = 360, minHeight = 100, children}) {
  return (
    <Box sx={{ p: 0, minWidth, minHeight, display: 'flex', flex: 1, position: 'relative' }}>
      <Box sx={{ width: '100%', height: '100%', flex: 1 }}>
        {children}
      </Box>
    </Box>
  );
});

export const TextWithAddon = React.memo(function TextWithAddon({text, addon}) {
  return (
    <Box sx={{display: 'flex', alignItems: 'center', gap: 1}}>
      <Typography fontSize='inherit'>{text}</Typography>
      {addon}
    </Box>
  );
});

export const MiniParagraph = React.memo(function MiniParagraph({text}) {
  return (
    <Typography variant="body2" sx={{mb: 1}}>{text}</Typography>
  );
});


export const ReadOnlySQL = React.memo(function ReadOnlySQL({value}) {
  const theme = useSelector((state) => state.settings.mode);
  return (
    <CodeMirror
      value={value}
      extensions={[sql()]}
      editable={false}
      theme={theme === 'dark' ? oneDark : 'light'}
      basicSetup={{
        lineNumbers: false,
        foldGutter: true,
      }}
    />
  );
});

export const TitledList = React.memo(function TitledList({ items }) {
  return (
    <List>
      {items.map((item, index) => (
        <ListItem key={index}>
          <ListItemText primary={item.title} secondary={item.value}  />
        </ListItem>
      ))}
    </List>
  );
});

export const TitledTextList = React.memo(function TitledTextList({dict}) {
  const items = _.map(dict, (value, key) => ({title: key, value}));
  return <TitledList items={items} />
});





export const BorderedBox = React.memo(function BorderedBox({sx, children}) {
  return (
    <Box sx={{border: '1px solid', borderColor: 'divider', borderRadius: 1, padding: 1, ...sx}}>
      {children}
    </Box>
  );
});

export const MiddleText = React.memo(function MiddleText({text}) {
  return (
    <Typography variant="body1" sx={{
      display: 'flex', color: 'inherit', alignItems: 'center',
      justifyContent: 'center', width: '100%', height: '100%',
      padding: 1,
      textAlign: 'center'}}>
      {text}
    </Typography>
  )
});


