import React from 'react';
import useSetting from '@/hooks/useSetting';
import { Box, Divider } from '@mui/material';
import CellRightClickCard from '@/components/table/rightClickMenu/CellRightClickCard';
import { AutoLink } from '@/components/elements/AutoLink';
import { TopButtons } from '@/components/table/rightClickMenu/TopButtons';


export const IFrameVisualizationCell = React.memo(function IFrameVisualizationCell({name='', value, colDef=null}) {
    const largeSize = 600;
    const url = value;

    const { value: size } = useSetting('tableMaxRowHeight');

    if (!url) return String(value);

    return (
        <Box>
            <iframe
                style={{width: size, height: size, pointerEvents: 'none'}}
                src={url + `&size=${size}`}
            >
            </iframe>
        <CellRightClickCard sx={{width: largeSize + 10}}>
            {name && colDef && <TopButtons name={name} value={value} colDef={colDef} showCopy={false} />}
            <Divider />
            <AutoLink url={url} />
            <iframe style={{width: largeSize, height: largeSize}} src={url + `&size=${largeSize}`}></iframe>
        </CellRightClickCard>
        </Box>
    );
});

