import { Box } from '@mui/material';
import ClickCard from '@/components/elements/ClickCard';
import NullPie from '@/components/table/ColumnStats/NullPie';
import React from 'react';
import { useSelector } from 'react-redux';
import { useSingleColumnMeta } from '@/hooks/useApiData';
import { isMissing } from '@/utils/helpers';


const CellWithQuickFilter = React.memo(({ name, quickFilter, mainFilter }) => {
  const height = useSelector((state) => state.settings.heightOfColumnDistribution);
  const meta = useSingleColumnMeta(name);

  if (isMissing(meta)) {
    return null;
  }

  const trigger = <NullPie columnName={name}/>
  const clickCard = (
    <ClickCard
      trigger={trigger}
      tooltip={null}
      title={`${name} : ${meta.duckdbType}`}
      width={600}
      height='auto'>
      {mainFilter}
    </ClickCard>
  );
  return (
    <Box sx={{
      width: '100%', height,
      display: 'flex', alignItems: 'center', justifyContent: 'center', gap: 1
      }}>
      <Box  sx={{flex: 0}}>
        {clickCard}
      </Box>
      <Box sx={{flex: 1, minWidth: 0}}>
        {quickFilter}
      </Box>
    </Box>
  );

});

CellWithQuickFilter.displayName = 'CellWithQuickFilter';

export default CellWithQuickFilter;