import { every } from 'lodash';
import path from 'path-browserify';
import { RENDER_TYPES } from '@/constants';

export const imageExtensions = ['.jpg', '.jpeg', '.png', '.gif', '.bmp', '.webp', '.svg', '.ico'];
export const tableExtensions = ['.csv', '.tsv', '.json', '.parquet', '.feather', '.arrow'];

export const isImage = (filePath) => {
   const ext = path.extname(filePath);
   return imageExtensions.includes(ext);
};

export const isTable = (filePath) => {
   const ext = path.extname(filePath);
   return tableExtensions.includes(ext);
};

export const isImageUrl = (str) => {
  if (!str || typeof str !== 'string') return false;

  // Check if string ends with common image extensions

  const lowerStr = str.toLowerCase();

  // Check if it's a URL pattern and ends with image extension
  const urlPattern = /^(\w+?:\/\/|\/)/i;
  return urlPattern.test(str) && imageExtensions.some(ext => lowerStr.endsWith(ext));
};


export const isJsonString = (str) => {
  if (!str || typeof str !== 'string') return false;
  try {
    JSON.parse(str);
    return true;
  } catch (e) {
    return false;
  }
}


export const decideRenderType = (exampleValues) => {
  if (exampleValues.length === 0) {
    return RENDER_TYPES.TEXT;
  } else if (every(exampleValues, (v) => isImageUrl(v))) {
    return RENDER_TYPES.IMAGE_URL;
  } else if (every(exampleValues, (v) => isJsonString(v))) {
    return RENDER_TYPES.JSON;
  } else {
    return RENDER_TYPES.TEXT;
  }
}