import { ResponsiveContainer, BarChart, Bar, Tooltip, XAxis, YAxis, Brush } from 'recharts';
import { Box, Typography } from '@mui/material';
import useMUITheme from '@/hooks/useMUITheme';
import React from 'react';

export const MyBarChart = React.memo(({
    columnStats, size=24, showBrush=false, onRangeChange=null
}) => {
    const muiTheme = useMUITheme();

    if (!columnStats) {
        return 'no data';
    }

    const {cnt_values, cnt_all, bin} = columnStats;
    const chartData = cnt_values;
    const labelCutoff = 90;


    const onBrushChange = ({startIndex, endIndex}) => {
        const startValue = startIndex * bin.step + bin.min;
        const endValue = (endIndex + 1) * bin.step + bin.min;
        if (onRangeChange !== null) {
            onRangeChange({startValue, endValue});
        }
      }

    const BarTooltip = ({ active, payload, label }) => {
        // Warning: it should be a Rechart bug. When I show Xaxis the lable is label, but when I hide it the label is index.

        if (label !== undefined && active && payload && payload.length) {
            const cnt = payload[0].value;
            const percentage = cnt_all > 0 ? ((cnt / cnt_all) * 100).toFixed(3) : 0;
            return (
                <Box sx={{ p: 1, bgcolor: 'background.card', borderRadius: 1, border: '1px solid', borderColor: muiTheme.palette.borderColor }}>
                    <Typography variant="body2" sx={{ mb: 0.5, fontWeight: 'bold' }}>Range {`${label}`}</Typography>
                    <Typography variant="body2">Count: {cnt.toLocaleString()}</Typography>
                    <Typography variant="body2">Percentage: {percentage}%</Typography>
                </Box>
            );
        }
        return null;
    };
    const brush = () => (
        <Brush
        tickFormatter={() => ''}
        dataKey="value"
        height={10}
        travellerWidth={6}
        stroke="CornflowerBlue"
        onChange={onBrushChange}
        tickerFormat={null}
        tickCount={10}
        />
    );

    return (
    <Box sx={{backgroundColor: 'background.card'}}>
        <ResponsiveContainer minWidth={24} minHeight={24} width={'100%'} height={size}>
            <BarChart data={chartData} barGap={0} barCategoryGap={0} >
                {size > labelCutoff && <XAxis dataKey="value" scale='auto' />}
                {size > labelCutoff && <YAxis dataKey="cnt" scale='auto'  />}
                {size > labelCutoff && (<Tooltip content={<BarTooltip />} isAnimationActive={false} /> )}
                <Bar
                    dataKey="cnt"
                    fill={muiTheme.palette.plotFill}
                    stroke={muiTheme.palette.text.secondary}
                    strokeWidth={1}
                    isAnimationActive={false}
                >

                </Bar>
                {size > labelCutoff && showBrush && brush()}
            </BarChart>
        </ResponsiveContainer>
    </Box>
    );
});

MyBarChart.displayName = 'MyBarChart';

export default MyBarChart;