import { useSelector } from 'react-redux';
import _ from 'lodash';

export const useDerivedColumn = (columnName) => {
  const derivedColumns = useSelector(state => state.derivedColumns);
  const dC = _.find(derivedColumns, (col) => col.name === columnName);
  return dC;
}

export const useDerivedColumnMeta = (columnName) => {
  const dC = useDerivedColumn(columnName);
  if (dC) {
    return {
      name: columnName, isDerived: true,
      isString: true, cellDataType: 'string',
    };
  }
  return null;
}

export const useDerivedColumnPreference = (columnName) => {
  const dC = useDerivedColumn(columnName);
  if (dC) {
    return {
      renderType: dC.renderType,
    };
  }
  return null;
}