import { Box, Divider, Typography } from '@mui/material';
import React from 'react';
import { HEIGHT_BOTTOM_BAR } from '@/constants';
import { IconColorMode } from '@/components/settings/IconColorMode';
import { IconFontSizeUp, IconFontSizeDown } from '@/components/settings/IconFontSize';
import { BottomIconJsonViewingSettings } from '@/components/bottomBar/JsonViewingSettings';
import { SX_TEXT_HOVER } from '@/utils/styles';
import { MyTooltip } from '@/components/elements/displays';
import { useState } from 'react';
import { useSelector } from 'react-redux';
import { IconLicense } from '@/components/elements/license/IconLicense';
import { IconEnableDebugging } from '@/components/settings/IconEnableDebugging';
import { SENSE_TABLE_PAGE } from '@/constants';
import NextLink from 'next/link';

export const VerticalDivider = () => {
    return <Divider orientation="vertical" flexItem />;
}

export const BottomSection = ({title, children}) => {
    return (
    <>
        <VerticalDivider />
        <Box sx={{
            flex: 0, display: 'flex', flexDirection: 'row', alignItems: 'center', gap: 0,
        }}>
            <Typography>{title}:</Typography>
            {children}
        </Box>
    </>
    )
}

export const BottomStatus = ({k, v, maxWidth=200}) => {
    const [copied, setCopied] = useState(false);

    const handleCopy = () => {
        navigator.clipboard.writeText(v);
        setCopied(true);
        setTimeout(() => {
            setCopied(false);
        }, 1000);
    }
    return (
        <>
            <VerticalDivider />
            <Box sx={{...SX_TEXT_HOVER, display: 'flex', flexDirection: 'row', alignItems: 'center', gap: 0.5}}>
                <Typography>{k}:</Typography>
                <MyTooltip title={copied ? 'Copied' : 'Click to copy to clipboard'}>
                    <Typography
                        className='truncate'
                        sx={{cursor: 'pointer', maxWidth: maxWidth}}
                        onClick={handleCopy}
                    >
                        {v}
                    </Typography>
                </MyTooltip>
            </Box>

        </>
    )
}

const BottomBarWrapper = React.memo(function BottomBarWrapper({children, settings}) {
    const mode = useSelector(state => state.settings.mode);

    const SectionSettings = () => {
        return (
            <BottomSection title="Settings">
                {settings}
                <IconEnableDebugging />
                <IconLicense />
                <BottomIconJsonViewingSettings />
                <IconFontSizeUp />
                <IconFontSizeDown />
                <IconColorMode />
            </BottomSection>
        )
    }


    return (
        <Box sx={{
            borderTop: '1px solid', borderColor: 'divider',
            display: 'flex', flexDirection: 'row', alignItems: 'center',
            justifyContent: 'space-between',
            height: HEIGHT_BOTTOM_BAR,
            px: 2, py: 0, gap: 1
        }}>
            <Box sx={{display: 'flex', flexDirection: 'row', alignItems: 'center', gap: 0.5}}>
                <NextLink href={SENSE_TABLE_PAGE} target="_blank">
                    <img src={`./SenseTable-${mode}.svg`} alt="SenseTable" style={{minWidth: 100, height: 20}} />
                </NextLink>

            </Box>

            {children}


            <SectionSettings />

        </Box>
    );
});

export default BottomBarWrapper;