import { Box, Popover, Card, CardHeader, CardContent } from '@mui/material';
import { useState } from 'react';
import CloseIcon from '@mui/icons-material/Close';
import OpenInFullIcon from '@mui/icons-material/OpenInFull';
import CloseFullscreenIcon from '@mui/icons-material/CloseFullscreen';
import WarningAmberIcon from '@mui/icons-material/WarningAmber';
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';
import { MyTooltip } from '@/components/elements/displays';
import { MyIconButton } from '@/components/elements/Controls';
import SettingsIcon from '@mui/icons-material/Settings';

const ClickCard = ({
  trigger, tooltip = 'Click to open', children, title, width = 400, height = 300,
  anchorVertical = 'bottom', anchorHorizontal = 'left', flex = 1,
  transformOriginVertical = 'top', transformOriginHorizontal = 'left',
  initMaximized = false,

}) => {
  const padding = 2;
  const headerHeight = 60;
  const [open, setOpen] = useState(false);
  const [anchorEl, setAnchorEl] = useState(null);
  const [isMaximized, setIsMaximized] = useState(initMaximized);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
    setOpen(true);
    event.stopPropagation();
  };

  const handleClose = () => {
    setOpen(false);
    setAnchorEl(null);
  };

  const handleMaximizeToggle = () => {
    setIsMaximized(!isMaximized);
  };

  const triggerElement = (
  <Box onClick={handleClick} sx={{ cursor: 'pointer', flex: flex }}>
    {trigger}
  </Box>
  );

  const cardStyle = isMaximized ? {
    width: '95vw',
    height: '95vh',
    margin: 0,
    top: 0,
    left: 0,
    position: 'fixed',
  } : {
    width: width + padding * 2 * 8, height: height + padding * 2 * 8 + headerHeight
  }

  return (
    <div style={{flex: flex, minWidth: 0}}>
      {tooltip ? <MyTooltip title={tooltip}>{triggerElement}</MyTooltip> : triggerElement}

      <Popover
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: anchorVertical,
          horizontal: anchorHorizontal,
        }}
        transformOrigin={{
          vertical: transformOriginVertical,
          horizontal: transformOriginHorizontal,
        }}
        slotProps={{
          paper: {sx: cardStyle},
        }}

      >
        <Card sx={{
          width: '100%',
          height: '100%',
          boxShadow: 3, position: 'relative',
          display: 'flex', flexDirection: 'column',
          backgroundColor: 'background.card',
          }}>
          <CardHeader
            title={title}
            sx={{padding: padding, height: headerHeight}}
            action={
              <>
                <MyIconButton icon={isMaximized ? <CloseFullscreenIcon /> : <OpenInFullIcon />} onClick={handleMaximizeToggle} />
                <MyIconButton icon={<CloseIcon />} onClick={handleClose} />
              </>
            }
          />
          <CardContent sx={{
            display: 'flex',
            flexDirection: 'column',
            flex: 1,
            p: padding,
            overflowY: 'auto',
          }}>
            {children}
          </CardContent>
        </Card>
      </Popover>
    </div>
  );
};

ClickCard.displayName = 'ClickCard';

export default ClickCard;

export const IconCard = ({title, children, icon, tooltip=null, iconSize='small', height=300}) => {
  const styledIcon = <MyIconButton icon={icon} iconSize={iconSize} />;
  return (
    <Box sx={{flex: 0}}>
      <ClickCard trigger={styledIcon} title={title} tooltip={tooltip} height={height}>
        <Box sx={{display: 'flex', flexDirection: 'column', gap: 2}}>
          {children}
        </Box>
      </ClickCard>
    </Box>
  );
};

IconCard.displayName = 'IconCard';

export const HelpCard = ({title, children}) => {
  return (
    <IconCard title={title} icon={<HelpOutlineIcon />} height={600}>
      {children}
    </IconCard>
  );
};

HelpCard.displayName = 'HelpCard';

export const WarningCard = ({title, children}) => {
  return (
    <IconCard title={title} icon={<WarningAmberIcon sx={{color: 'warning.main'}} />}>
      {children}
    </IconCard>
  );
};

WarningCard.displayName = 'WarningCard';

export const InfoCard = ({title, children}) => {
  return (
    <IconCard title={title} icon={<InfoOutlinedIcon />}>
      {children}
    </IconCard>
  );
};

InfoCard.displayName = 'InfoCard';

export const SettingsCard = ({title, children, height=300}) => {
  return (
    <IconCard title={title} icon={<SettingsIcon />} height={height}>
      {children}
    </IconCard>
  );
};

SettingsCard.displayName = 'SettingsCard';