import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Box, ImageList, ImageListItem, Button, Typography } from '@mui/material';
import { updateChildren, pathJoin } from '@/store/folderBrowserSlice';
import { useEffect } from 'react';
import ChildrenStats from '@/components/folderBrowser/folderViewers/ChildrenStats';
import { createSelector } from '@reduxjs/toolkit';
import { locateItem, toggleItemExpansion } from '@/store/folderBrowserSlice';
import { isImage } from '@/utils/typesUtils';
import _ from 'lodash';
import { setViewingItemId } from '@/store/folderBrowserSlice';
import { SX_TEXT_HOVER, SX_WIDTH_100, SX_BORDER } from '@/utils/styles';
import { getFileUrl } from '@/utils/urls';
import { pathBasename } from '@/store/folderBrowserSlice';

const FolderPeekItem = React.memo(function FolderPeekItem({id}) {
    const dispatch = useDispatch();
    const items = useSelector(createSelector([
        state => state.folderBrowser.treeData,
    ], (treeData) => {
        return locateItem(treeData, id).children;
    }));
    const rootFolder = useSelector(state => state.settings.folderBrowserRootFolder);
    const peekIntoSubfolders = useSelector(state => state.settings.folderBrowserPeekIntoSubfolders);


    useEffect(() => {
        dispatch(updateChildren(id));
    }, [id, dispatch]);

    switch (peekIntoSubfolders) {
        case 'none':
            return null;
        case 'count_by_extension':
            return (
                <Box sx={{width: '100%'}}>
                    <ChildrenStats items={items} />
                </Box>
            );
        case 'images':
            const imageList = items.filter(it => isImage(it.id)).map(it => pathJoin(rootFolder, it.id));
            const imageSamples = imageList.slice(0, 4);
            if (_.isEmpty(imageSamples)) {
                return null;
            }
            return (
                <ImageList
                    sx={{width: '100%', overflow: 'hidden'}}
                    cols={2}
                >
                    {imageSamples.map(it => <ImageListItem key={it}>
                        <img src={getFileUrl(it)}
                             style={{width: '100%', aspectRatio: '1 / 1', objectFit: 'cover'}}
                             alt={pathBasename(it)} />
                    </ImageListItem>)}
                </ImageList>
            );
    }
});

const FolderCardItem = React.memo(function FolderCardItem({id}) {
    const dispatch = useDispatch();
    const peekIntoSubfolders = useSelector(state => state.settings.folderBrowserPeekIntoSubfolders);
    const onFolderClick = () => {
        dispatch(setViewingItemId(id));
        dispatch(toggleItemExpansion(id));
    }
    return (
        <Box sx={{
            display: 'flex', flexDirection: 'column', gap: 1, alignItems: 'left',
            ...SX_BORDER, padding: 1,
            ...SX_WIDTH_100,
            backgroundColor: 'background.card',
        }}>
            <Button
              variant="text"
              sx={{ textTransform: 'none', ...SX_TEXT_HOVER, justifyContent: 'flex-start' }}
              onClick={onFolderClick}>
                <Typography className='truncate'>
                    {pathBasename(id)}
                </Typography>
            </Button>
            {peekIntoSubfolders !== 'none' && <FolderPeekItem id={id} />}
        </Box>
    );
})

const FolderPeek = React.memo(function FolderPeek({items}) {
    return (
        <Box sx={{
            columnCount: 4,
            columnGap: 1,
            rowGap: 1,

        }}>
            {items.filter(it => it.isDir).map(it => (
                <Box key={it.id} sx={{
                    display: 'grid', gridTemplateRows: '1fr auto', breakInside: 'avoid', marginBottom: 1,
                    ...SX_WIDTH_100,
                }}>
                    <FolderCardItem key={it.id} id={it.id} />
                </Box>
            ))}
        </Box>
    )
});

export default FolderPeek;