import React from 'react';
import { Box } from '@mui/material';
import PanelControlsWrapper from '@/components/elements/PanelControlsWrapper';
import { HelpCard } from '@/components/elements/ClickCard';
import { SectionHeader } from '@/components/elements/displays';
import { KeyboardShortcutsTable, ShortcutRow } from '@/components/elements/KeyboardShortcutsTable';
import EastOutlinedIcon from '@mui/icons-material/EastOutlined';
import NorthOutlinedIcon from '@mui/icons-material/NorthOutlined';
import WestOutlinedIcon from '@mui/icons-material/WestOutlined';
import SouthOutlinedIcon from '@mui/icons-material/SouthOutlined';
import DropdownInSettings from '@/components/settings/DropdownInSettings';
import { useDispatch, useSelector } from 'react-redux';
import CheckboxInSettings from '@/components/settings/CheckboxInSettings';
import { updateChildren, setExpandedItems } from '@/store/folderBrowserSlice';

const FolderNavHelpCard = React.memo(function FolderNavHelpCard({}) {

    return (
        <HelpCard title="Folder Navigation Help">
            <SectionHeader title="Keyboard Shortcuts" />
            <KeyboardShortcutsTable>
                <ShortcutRow
                    keys={[<NorthOutlinedIcon key="north"/>]}
                    descriptions={['Move to the previous item in the list.']}
                />
                <ShortcutRow
                    keys={[<SouthOutlinedIcon key="south"/>]}
                    descriptions={['Move to preview the next item in the list.']}
                />
                <ShortcutRow
                    keys={[<WestOutlinedIcon key="west"/>]}
                    descriptions={[
                        'If you are viewing a file, it will change focus to its parent folder.',
                        'If you are viewing a folder, it will collapse the folder view.'
                    ]}
                />
                <ShortcutRow
                    keys={[<EastOutlinedIcon key="east"/>]}
                    descriptions={['If you are viewing a folder, it will expand the folder and change focus to its child.']}
                />
            </KeyboardShortcutsTable>
        </HelpCard>
    );
});

export const FolderNavControls = React.memo(function FolderNavControls({}) {
    const dispatch = useDispatch();
    const viewingItemId = useSelector(state => state.folderBrowser.viewingItemId);

    const onShowHiddenChange = (value) => {
        dispatch(updateChildren(''));
        dispatch(updateChildren(viewingItemId));
        dispatch(setExpandedItems([]));
    }
    return (
        <PanelControlsWrapper>
            <FolderNavHelpCard />
            <Box sx={{flex: 1}} >
                <DropdownInSettings settingKey="folderNavigationShowFileInfo" shortLabel="Show info" />
            </Box>
            <Box sx={{flex: 0}}>
            <CheckboxInSettings
                settingKey="folderBrowserShowHiddenFiles"
                shortLabel="Show hidden files"
                extraChange={onShowHiddenChange}
            />
            </Box>



        </PanelControlsWrapper>
    );
});