import { useSelector, useDispatch } from 'react-redux';
import { useState } from 'react';
import { TextField } from '@mui/material';
import { setColumnFilterAndRefreshData, removeColumnFilterAndRefreshData } from '@/store/agColDefsSlice';
import React from 'react';

const MiniTextFilter = React.memo(({ name }) => {
  const dispatch = useDispatch();
  const reduxContains = useSelector(state => state.agColDefs.filters[name])?.contains || '';
  const [filterContains, setFilterContains] = useState(reduxContains);


  const applyFilters = () => {

    if (filterContains.trim() === '') {
      if (reduxContains === '') {
        return;
      } else {
        removeColumnFilterAndRefreshData(dispatch, name);
      }

    } else {
      const filter = {
        filterType: 'myText',
        contains: filterContains,
        null: 'exclude'
      };
      setColumnFilterAndRefreshData(dispatch, name, filter);
    }

  }

  return (

    <TextField
      hiddenLabel={Boolean(filterContains)}
      size='small'
      type='text'
      label={filterContains ? null : 'Filter by text containing'}
      variant='filled'
      value={filterContains}
      onChange={(e) => setFilterContains(e.target.value)}
      onKeyDown={(e) => {
          if (e.key === 'Enter') {
            applyFilters();
          }
        }}
      onBlur={applyFilters}
      sx={{width: '100%'}}
    />



 );
});

MiniTextFilter.displayName = 'MiniTextFilter';

export default MiniTextFilter;