import { Box, Typography, Dialog, DialogTitle, DialogContent, DialogActions } from "@mui/material";
import React from "react";
import KeyIcon from '@mui/icons-material/Key';
import { BottomIconCard } from "@/components/bottomBar/BottomIconCard";
import { useState, useEffect } from "react";
import { MyButton } from "@/components/elements/Controls";
import { verifyLicense } from "@/components/elements/license/verifyLicense";
import _ from "lodash";
import { TitledList } from "@/components/elements/displays";
import { SectionHeader } from "@/components/elements/displays";
import useSetting from "@/hooks/useSetting";
import { isMissing } from "@/utils/helpers";

const LicenseInfo = ({license}) => {
  const infoList = _.map(license, (value, key) => ({title: key, value: value || 'N/A'}));
  return <TitledList items={infoList} />
}

export const IconLicense = React.memo(function IconLicense() {
    const { value: licenseKey } = useSetting('licenseKey');

    const [open, setOpen] = useState(true);
    const [errors, setErrors] = useState([]);
    const [licenseFailed, setLicenseFailed] = useState(null);
    const [license, setLicense] = useState({});

    const handleClose = () => {
      setOpen(false);
    };


    useEffect(() => {

      const performVerify = async () => {
        const {errors, license} = await verifyLicense({licenseKey});
        setErrors(errors);
        setLicense(license);
        setLicenseFailed(errors.length > 0);
      }
      if (isMissing(licenseFailed) && !isMissing(licenseKey)) {
        performVerify();
      }
    }, [licenseFailed, licenseKey])

    if (isMissing(licenseFailed) || isMissing(licenseKey)) {
      return null;
    } else if (errors.length === 0) {
      return (
        <BottomIconCard title="License" icon={<KeyIcon/>} height={200}>
          <Box sx={{display: 'flex', flexDirection: 'column', gap: 2, width: '100%', height: '100%'}}>
            <LicenseInfo license={license} />
          </Box>
        </BottomIconCard>
      )
    } else {
      return (
        <Dialog open={open} onClose={handleClose}>
          <DialogTitle sx={{color: 'error.main'}}>
            License is invalid
          </DialogTitle>
          <DialogContent>
            <SectionHeader title="License granted to" />
            <LicenseInfo license={license} />

            <SectionHeader title="Errors" />
              {errors.map((error, index) => (
                <Typography key={index}>{error}</Typography>
              ))}

              <Typography sx={{my: 2, color: 'warning.light'}}>
                For evaluation purpose, you can use <code>localhost</code> or <code>127.0.0.1</code> as the domain.
              </Typography>
          </DialogContent>
          <DialogActions>
            <MyButton onClick={handleClose}>
              I will purchase license later
            </MyButton>
          </DialogActions>
        </Dialog>
      );
    }
});