import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useSingleColumnMeta } from '@/hooks/useApiData';
import { decideRenderType } from '@/utils/typesUtils';
import _ from 'lodash';
import { isMissing } from '@/utils/helpers';
import { setColumnPreference } from '@/store/columnPreferenceSlice';
import { useCardinality } from '@/hooks/useCardinality';
import { RENDER_TYPES } from '@/constants';
import { useDerivedColumnPreference } from '@/hooks/useDrivedColumn';
import { createSelector } from 'reselect';

function recommendRenderType(colMeta, thresholdCategorical, cntD) {

  const ratioCondition = !isMissing(cntD) && (cntD / colMeta.cntAll) < 0.5;
  const countCondition = cntD <= thresholdCategorical;
  const isCategorical = ratioCondition && countCondition;
  const exampleValues = [colMeta.min, colMeta.max];

  if (colMeta.isDatetime) {
    return RENDER_TYPES.TEXT;
  } else if (!colMeta.isPrimitive) {
    return RENDER_TYPES.JSON;
  } else if (colMeta.isString) {
    return isCategorical ? RENDER_TYPES.CATEGORY : decideRenderType(exampleValues);
  } else if (colMeta.isNumeric) {
      return isCategorical && colMeta.isInteger ? RENDER_TYPES.CATEGORY : RENDER_TYPES.NUMBER;
  } else if (colMeta.isBoolean) {
      return RENDER_TYPES.CATEGORY;
  } else {
    return decideRenderType(exampleValues);
  }

}


export const useAllRenderTypes = () => {
  const renderTypeDict = useSelector(createSelector([
    state => state.columnPreference.data,
    state => state.derivedColumns,
  ], (colPrefs, derivedColumns) => {
    const regularTypes = _.mapValues(colPrefs, (colPref, colName) => {
      return colPref.renderType;
    });
    const derivedTypes = _(derivedColumns || []).map(item => [item.name, item.renderType]).fromPairs().value();
    return _.merge(regularTypes, derivedTypes);
  }));

  return renderTypeDict;
};

export const useNumberColumns = () => {
  const renderTypeDict = useAllRenderTypes() || {};
  return _.filter(_.keys(renderTypeDict), (k) => renderTypeDict[k] === RENDER_TYPES.NUMBER);
};


export const useSingleColumnPreference = (columnName) => {
  const dispatch = useDispatch();
  const meta = useSingleColumnMeta(columnName);
  const data = useSelector(state => state.columnPreference.data[columnName]);
  const dC = useDerivedColumnPreference(columnName);

  const thresholdCategorical = useSelector(state => state.settings.thresholdCategorical);
  const { data: cardinalityData } = useCardinality(columnName) || {};

  const shouldInit = !isMissing(meta) && isMissing(dC) && !isMissing(cardinalityData) && (isMissing(data) || _.isEmpty(data));

  useEffect(() => {
    if (shouldInit) {
      const {cntD} = cardinalityData;
      const renderType = recommendRenderType(meta, thresholdCategorical, cntD);

      dispatch(setColumnPreference({name: columnName, renderType}));
    }
  }, [dispatch, shouldInit, meta, thresholdCategorical, cardinalityData, columnName]);

  if (dC) {
    return dC;
  } else {
    return data;
  }
};


export const pickColumnsByRenderTypeList = ({state, renderTypeList}) => {
  const allTypes = state.columnPreference.data || {};
  const found = _.keys(_.pickBy(allTypes, (v, k) => renderTypeList.includes(v?.renderType)));
  return found;
}