import { verify, etc } from '@noble/ed25519';
import { sha512 } from '@noble/hashes/sha2.js';

// Set SHA-512 for ed25519
etc.sha512Sync = (...m) => sha512(etc.concatBytes(...m))

function b64urlToBytes(b64url) {
  try {
    // 1. Replace URL-safe chars
    let b64 = b64url.replace(/-/g, '+').replace(/_/g, '/')

    // 2. Re-add padding if its missing
    while (b64.length % 4) b64 += '='

    // 3. Decode with atob  Uint8Array
    const binary = atob(b64)
    return Uint8Array.from(binary, ch => ch.charCodeAt(0))
  } catch (error) {
    console.error(error);
    return null;
  }
}

function b64UrlToStr(b64url) {
  return atob(b64url.replace(/-/g, '+').replace(/_/g, '/'));
}

export const verifyLicense = async ({licenseKey}) => {
  const actualDomain = window.location.hostname;
  const invalidLicense = {domain: null, url_prefix: null, valid_until: null};

  if (!licenseKey) {
    return {errors: ['No license key'], license: invalidLicense};
  }

  if (['localhost', '127.0.0.1'].includes(actualDomain)) {
    // No license check for localhost
    return {errors: [], license: {
      domain: actualDomain,
      url_prefix: '*',
      valid_until: '2099-01-01',
    }};
  }

  const [signature, publicKey, messageCode] = licenseKey.split('|');
  if (!signature || !publicKey || !messageCode) {
    return {errors: ['Invalid license key'], license: invalidLicense};
  }


  const messageBytes = b64urlToBytes(messageCode);
  const signatureBytes = b64urlToBytes(signature);
  const publicKeyBytes = b64urlToBytes(publicKey);
  if (!signatureBytes || !publicKeyBytes || !messageBytes) {
    return {errors: ['Invalid license key'], license: invalidLicense};
  }
  const valid = await verify(signatureBytes, messageBytes, publicKeyBytes);
  if (!valid) {
    return {errors: ['The license key is invalid'], license: invalidLicense};
  }

  const {domain, url_prefix, valid_until} = JSON.parse(b64UrlToStr(messageCode));
  const dateNow = new Date();
  const validUntilDate = new Date(valid_until);

  const errors = [];

  if (domain !== actualDomain) {
    errors.push(`Actual domain ${actualDomain} does not match the licensed domain ${domain}`);
  }
  if (dateNow > validUntilDate) {
    errors.push(`License expired on ${valid_until}`);
  }
  if (!window.location.pathname.startsWith(url_prefix)) {
    errors.push(`Actual url prefix ${window.location.pathname} does not match the licensed url prefix ${url_prefix}`);
  }

  return {errors, license: {domain, url_prefix, valid_until}};
}
