import { useDispatch } from 'react-redux';
import FilterAltIcon from '@mui/icons-material/FilterAlt';
import FilterAltOutlinedIcon from '@mui/icons-material/FilterAltOutlined';
import { useSelector } from 'react-redux';
import ClickCard from '@/components/elements/ClickCard';
import { Box } from '@mui/material';
import { parseFilterItem } from '@/utils/sql/parseFilterItem';
import { ReadOnlySQL } from '@/components/elements/displays';
import { MyButton, MyIconButton } from '@/components/elements/Controls';
import { removeColumnFilterAndRefreshData } from '@/store/agColDefsSlice';

const ColumnFilterIcon = ({columnName}) => {
    const dispatch = useDispatch();
    const reduxFilter = useSelector(state => state.agColDefs.filters[columnName]);

    const clearFilters = () => {
        removeColumnFilterAndRefreshData(dispatch, columnName);
      }

    const icon = reduxFilter ? <FilterAltIcon sx={{color: 'primary.main'}} /> : <FilterAltOutlinedIcon/>;
    const trigger = <MyIconButton icon={icon}/>;
    const tooltip = reduxFilter ? 'Active ColumnFilter' : 'No active ColumnFilter';
    const content = reduxFilter ? <ReadOnlySQL value={parseFilterItem(columnName, reduxFilter)}/> : null;

    return <Box>
        <ClickCard trigger={trigger} title={tooltip} tooltip={tooltip}>
            <Box sx={{display: 'flex', flexDirection: 'column', gap: 1}}>
                {content}
            </Box>
            <Box sx={{flex: 1}}></Box>

            <Box sx={{display: 'flex', justifyContent: 'flex-start'}}>
                <MyButton onClick={clearFilters} color="error">Clear ColumnFilter</MyButton>
            </Box>
        </ClickCard>
    </Box>;



};

export default ColumnFilterIcon;

