import { useSelector, useDispatch } from 'react-redux';
import { useSingleColumnPreference } from '@/hooks/useColumnPreference';
import { baselineColumnStatsSlice, filteredColumnStatsSlice } from '@/store/dataSlice';
import { isMissing } from '@/utils/helpers';
import { runQuery } from '@/utils/api';
import { useEffect } from 'react';
import _ from 'lodash';
import useFilters from '@/hooks/useFilters';
import { updateSqlRecord } from '@/store/sqlSlice';
import { useSingleColumnMeta } from '@/hooks/useApiData';
import { sanitizeName } from '@/utils/sql/helpers';
import { columnHistogramQuery, columnCategoryQuery } from '@/utils/sql/columnStats';
import { updateCountFiltered, updateCountBaseline } from '@/store/viewingSlice';
import { padItems } from '@/utils/histogramBins';
import { RENDER_TYPES } from '@/constants';

const useColumnStatsQuery = ({columnName, applyFilters=false}) => {
  const {baselineFiltersSql, allFiltersSql} = useFilters();
  const filterSql = applyFilters ? allFiltersSql : baselineFiltersSql;
  const tableExpr = useSelector(state => state.dataFile.tableExpr);
  const meta = useSingleColumnMeta(columnName);
  const {renderType} = useSingleColumnPreference(columnName) || {};
  const thresholdCategorical = useSelector(state => state.settings.thresholdCategorical);
  const histogramNumberOfBins = useSelector(state => state.settings.histogramNumberOfBins);

  const resultTransformer = (result) => {
    const cnt_values = result.filter(r => r.value !== null);
    const cnt_all = _(result).map('cnt').sum();
    const cnt_null = result.find(r => r.value === null)?.cnt || 0;
    const cnt_not_null = cnt_all - cnt_null;
    return {cnt_values, cnt_all, cnt_null, cnt_not_null};
  }

  const filteredColumnTableExpr = `SELECT ${sanitizeName(columnName)} AS value FROM ${tableExpr} WHERE ${filterSql || 'TRUE'}`;

  switch (renderType) {
    case RENDER_TYPES.CATEGORY:
      return {
        resultTransformer: (result) => result[0],
        query: columnCategoryQuery({filteredColumnTableExpr, thresholdCategorical})
      };

    case RENDER_TYPES.NUMBER:
      if (meta.allNull) {
        return {};
      }
      const histogramTransformer = (result) => {
        result = result[0];
        const { min, step, round_to } = result.bin;
        const cnt_values = padItems({min, step, round_to, cntValues: result.cnt_values});
        return {...result, cnt_values};
      }

      return {
        resultTransformer: histogramTransformer,
        query: columnHistogramQuery({filteredColumnTableExpr, histogramNumberOfBins})
      };

    case RENDER_TYPES.TEXT:

      return { resultTransformer, query: `
      SELECT UNNEST([
        {'value': 'not_null', 'cnt': COUNT_IF(${sanitizeName(columnName)} IS NOT NULL)},
        {'value': NULL, 'cnt': COUNT_IF(${sanitizeName(columnName)} IS NULL) }
        ], RECURSIVE:=TRUE)
      FROM ${tableExpr}
      WHERE ${filterSql || 'TRUE'}
      `};
    default:
      return {};
  }

}


const fetchColumnStatsData = ({
  columnName, actions, sqlKey, reduxKey, query, resultTransformer, totalCountAction
}) => async (dispatch, getState) => {
  const { data, loading, error, shouldRefresh } = getState()[reduxKey][columnName] || {};
  if ((data || loading) && !shouldRefresh) return; //  Already loading or loaded  skip!

  dispatch(actions.fetchStart({name: columnName}));

  try {
    const result = await runQuery({dispatch, key: sqlKey, query});
    const transformedResult = resultTransformer(result);
    dispatch(actions.fetchSuccess({name: columnName, ...transformedResult}));
    const {cnt_all } = transformedResult || {};
    if (cnt_all) {
      dispatch(totalCountAction(cnt_all));
    }
  } catch (err) {
    console.error(`error fetching baseline column stats: ${columnName}}`, err);
    dispatch(actions.fetchError({error: err.message, name: columnName}));
  }
};


export const useBaselineColumnStats = (columnName) => {
  const dispatch = useDispatch();
  const actions = baselineColumnStatsSlice.actions;
  const {query, resultTransformer} = useColumnStatsQuery({columnName, applyFilters: false});

  const reduxKey = 'baselineColumnStats';
  const sqlKey = `baseStats-${columnName}`;
  const { data, loading, error, shouldRefresh } = useSelector(state => state[reduxKey][columnName]) || {};

  const shouldInit = isMissing(data) && !loading && !error && !isMissing(query);

  useEffect(() => {
    if (shouldInit || shouldRefresh) {
      dispatch(fetchColumnStatsData({
        columnName, actions, sqlKey, reduxKey, query, resultTransformer,
        totalCountAction: updateCountBaseline
      }));
    }
  }, [dispatch, shouldInit, shouldRefresh, columnName, query, actions, sqlKey, reduxKey, resultTransformer]);

   return {data, loading, error, sqlKey};

}

export const useFilteredColumnStats = (columnName) => {
  const dispatch = useDispatch();
  const actions = filteredColumnStatsSlice.actions;
  const {hasFilters, allFiltersSql} = useFilters();
  const {query, resultTransformer} = useColumnStatsQuery({columnName, applyFilters: true});


  const reduxKey = 'filteredColumnStats';
  const sqlKey = `fltStats-${columnName}`;
  const baselineStats = useBaselineColumnStats(columnName);
  const baseSqlRecord = useSelector(state => state.sql[baselineStats.sqlKey]);
  const { data, loading, error, shouldRefresh } = useSelector(state => state[reduxKey][columnName]) || {};

  const shouldInit = isMissing(data) && !loading && !error && !isMissing(query) && !isMissing(baselineStats.data);

  useEffect(() => {
    if (shouldInit || shouldRefresh) {
      if (!hasFilters) {
        dispatch(actions.fetchSuccess(baselineStats.data));
        dispatch(updateSqlRecord({key: sqlKey, records: baseSqlRecord}));
        dispatch(updateCountFiltered(baselineStats.data.cnt_all));
      } else {
        dispatch(fetchColumnStatsData({
          columnName, actions, sqlKey, reduxKey, query, resultTransformer,
          totalCountAction: updateCountFiltered
        }));
      }
    }
  }, [dispatch, shouldInit, shouldRefresh, columnName, hasFilters, allFiltersSql, query, actions, sqlKey, reduxKey, baselineStats.data, baseSqlRecord, resultTransformer]);

   return {data, loading, error, sqlKey};
}
