import { useDispatch } from 'react-redux';
import { updateSettings } from '@/store/settingsSlice';
import { MyTooltip } from '@/components/elements/displays';
import { Box, Typography, FormControl, InputLabel, Select, MenuItem, CircularProgress } from '@mui/material';
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';
import { zIndex } from '@/utils/zIndex';
import useSetting from '@/hooks/useSetting';
import { isMissing } from '@/utils/helpers';

const DropdownInSettings = ({settingKey, shortLabel=null}) => {
    const dispatch = useDispatch();
    const {schema, initValue, value} = useSetting(settingKey);

    if (isMissing(initValue)) {
        return <CircularProgress />;
    }

    const changeValue = (value) => {
        dispatch(updateSettings({[settingKey]: value}));
    }
    const labelElement = (
        <Box sx={{display: 'flex', alignItems: 'center', gap: 1}}>
            <MyTooltip title={settingKey}><InfoOutlinedIcon fontSize="small"/></MyTooltip>
            <Typography fontSize="inherit">{shortLabel || schema.description}</Typography>
        </Box>
    );

    return (
        <FormControl sx={{ mt: 1, width: '100%'}} size="small">
            <InputLabel>{labelElement}</InputLabel>
            <Select
                value={value}
                onChange={(e) => changeValue(e.target.value)}
                label={labelElement}
                MenuProps={{
                    sx: {zIndex: zIndex.alwaysOnTop}
                }}
            >
            {schema.enum.map((option, idx) => (
                <MenuItem key={`${settingKey}-${idx}`} value={option}>{option}</MenuItem>
            ))}
            </Select>
        </FormControl>
    )
}

export default DropdownInSettings;

