import { Box } from '@mui/material';
import ClickCard from '@/components/elements/ClickCard';
import NullPie from '@/components/table/ColumnStats/NullPie';
import React from 'react';
import { useSelector } from 'react-redux';
import { useSingleColumnMeta } from '@/hooks/useApiData';
import { isMissing } from '@/utils/helpers';

const CellWithDistribution = React.memo(({ name, data, miniVisual, mainVisual, cardHeight='auto' }) => {
    const height = useSelector((state) => state.settings.heightOfColumnDistribution);
    const meta = useSingleColumnMeta(name);

    if (isMissing(meta)) {
        return null;
    }

      const trigger = (
        <Box
          className={'card-trigger'}
          sx={{width: '100%', height, display: 'flex', alignItems: 'center', justifyContent: 'center', gap: 1}}
        >
          <Box sx={{flex: 0}}>
            <NullPie columnName={name}/>
          </Box>
          <Box sx={{flex: 1, minWidth: 0}}>
            {miniVisual}
          </Box>
        </Box>
      );

      return (
        <ClickCard trigger={trigger} tooltip={null} title={`${name} : ${meta.duckdbType}`} width={600} height={cardHeight}>
          {mainVisual}
        </ClickCard>
      );
});

CellWithDistribution.displayName = 'CellWithDistribution';

export default CellWithDistribution;