import React from 'react';
import { Breadcrumbs, Link, Typography } from '@mui/material';
import path from 'path-browserify';
import { useDispatch, useSelector } from 'react-redux';
import { setViewingItemId } from '@/store/folderBrowserSlice';
import { Box } from '@mui/material';
import { SX_TEXT_HOVER, SX_WIDTH_100, SX_BORDER } from '@/utils/styles';

export const BreadCrumbLink = ({onClick, label}) => {
    return (
        <Link sx={{cursor: 'pointer', ...SX_TEXT_HOVER}} onClick={onClick} underline='hover'>
            <Typography className='truncate'>
                {label}
            </Typography>
        </Link>
    )
}

const BreadCrumbsNav = React.memo(function BreadCrumbsNav({id, isDir=false}) {
    const dispatch = useDispatch();
    const rootFolder = useSelector(state => state.settings.folderBrowserRootFolder);
    const segments = id.split(path.sep);

    return (
        <Box sx={{
            display: 'flex', flexDirection: 'row', gap: 1, alignItems: 'center',
            ...SX_WIDTH_100, backgroundColor: 'background.card', p: 1, ...SX_BORDER,
        }}>
            <Breadcrumbs sx={{
                ...SX_TEXT_HOVER,
                ...SX_WIDTH_100,
            }}>
                <BreadCrumbLink onClick={() => dispatch(setViewingItemId(''))} label={path.basename(rootFolder)} />
                {segments.map((segment, index) => {
                    const isLast = index === segments.length - 1;
                    return (
                    <Link
                        key={index}
                        underline={isLast ? 'none' : 'hover'}
                        sx={{cursor: isLast ? 'default' : 'pointer', ...SX_WIDTH_100, ...(isLast ? {color: 'text.primary'} : SX_TEXT_HOVER)}}
                        onClick={() => {
                            if (!isLast) {
                                dispatch(setViewingItemId(segments.slice(0, index + 1).join(path.sep)));
                            }
                        }}
                    >
                        <Typography className='truncate'>
                            {segment}
                        </Typography>
                    </Link>
                    );
                })}
            </Breadcrumbs>

        </Box>
    );
});

export default BreadCrumbsNav;