import { useDispatch } from 'react-redux';
import { updateDef } from '@/store/agColDefsSlice';
import PushPinOutlinedIcon from '@mui/icons-material/PushPinOutlined';
import PushPinIcon from '@mui/icons-material/PushPin';
import { IconDropdown } from '@/components/elements/IconDropdown';
import { MyIconButton } from '@/components/elements/Controls';
import React from 'react';
import { SX_TEXT_HOVER } from '@/utils/styles';


const PIN_ICONS = ({hide = false, pinned}) => {
  const activeStyle = hide ? {
    ...SX_TEXT_HOVER,
    opacity: 0.5
  } : {
    color: 'primary.main',
    '&:hover': {
      color: 'primary.light'
    }
  };
  const icons = {
    left: <PushPinIcon sx={{transform: 'rotate(45deg)', ...activeStyle}}/>,
    right: <PushPinIcon sx={{transform: 'rotate(-45deg)', ...activeStyle}}/>,
    no: <PushPinOutlinedIcon />
  };
  return icons[pinned] || icons.no;
}

export const ToggleColumnPinIcon = React.memo(({columnName, pinned, hide}) => {
  const dispatch = useDispatch();

  const icon = PIN_ICONS({hide, pinned});
  const nextValue = {'left': 'right', 'right': 'no', 'no': 'left'}[pinned] || 'left';

  const togglePin = () => {
    dispatch(updateDef({field: columnName, name: 'pinned', value: nextValue}));
  }

  return <MyIconButton icon={icon} onClick={togglePin} disabled={hide}/>;
});


export const DropdownColumnPinIcon = React.memo(({columnName, pinned}) => {
  const dispatch = useDispatch();


  const updatePin = (newPinned) => {
    dispatch(updateDef({field: columnName, name: 'pinned', value: newPinned}));
  }

  const choices = [
    {
      icon: PIN_ICONS({pinned: 'left'}),
      label: 'Pin to the left',
      value: 'left'
    },
    {
      icon: PIN_ICONS({pinned: 'no'}),
      label: 'Do not pin',
      value: 'no'
    },
    {
      icon: PIN_ICONS({pinned: 'right'}),
      label: 'Pin to the right',
      value: 'right'
    }
  ]

  return (
    <IconDropdown
      title="Pin to always visible?"
      choices={choices}
      selectedValue={pinned || 'no'}
      onChange={updatePin}
    />
  );
});

ToggleColumnPinIcon.displayName = 'ToggleColumnPinIcon';
DropdownColumnPinIcon.displayName = 'DropdownColumnPinIcon';
