import React from 'react';
import { proxyedUrl } from '@/utils/urls';
import NextLink from 'next/link';
import { Box, Typography } from '@mui/material';
import OpenInNewOutlinedIcon from '@mui/icons-material/OpenInNewOutlined';
import { MyIconButton } from '@/components/elements/Controls';



export const OpenUrlButton = React.memo(function OpenUrlButton({url}) {
    return (
        <NextLink href={url} target="_blank" sx={{flex: 0}}>
            <MyIconButton icon={<OpenInNewOutlinedIcon />} tooltip="Open in new tab" />
        </NextLink>
    )
})

export const AutoLink = React.memo(function AutoLink({url}) {
    const proxyUrl = proxyedUrl(url);
    return (
      <Box sx={{display: 'flex', alignItems: 'center', gap: 0.5, maxWidth: '100%'}}>
        <OpenUrlButton url={proxyUrl} />

        <Typography variant="body2" sx={{color: 'text.secondary', fontFamily: 'monospace'}} className="truncate">
        {url}
        </Typography>


      </Box>
    );
  });

