import * as React from 'react';
import Backdrop from '@mui/material/Backdrop';
import SpeedDial from '@mui/material/SpeedDial';
import Typography from '@mui/material/Typography';
import SpeedDialIcon from '@mui/material/SpeedDialIcon';
import SpeedDialAction from '@mui/material/SpeedDialAction';
import ShareOutlinedIcon from '@mui/icons-material/ShareOutlined';
import HelpOutlineOutlinedIcon from '@mui/icons-material/HelpOutlineOutlined';
import { HEIGHT_BOTTOM_BAR } from '@/constants';
import BackHandOutlinedIcon from '@mui/icons-material/BackHandOutlined';

const actions = [
    { icon: <ShareOutlinedIcon />, name: 'Share' },
    { icon: <BackHandOutlinedIcon />, name: 'Try with my data' },
    { icon: <HelpOutlineOutlinedIcon />, name: 'Help' },
  ];

export default function TopSpeedDial() {
    const [open, setOpen] = React.useState(false);
    const handleOpen = () => setOpen(true);
    const handleClose = () => setOpen(false);
    return null;

    return (
        <>
        <Backdrop open={open} />
        <SpeedDial
            ariaLabel="SpeedDial"
            sx={{
                position: 'absolute', bottom: HEIGHT_BOTTOM_BAR + 16, right: 16,
            }}
            icon={<SpeedDialIcon />}
            onClose={handleClose}
            onOpen={handleOpen}
            open={open}
        >
            Message
            {actions.map((action) => (
            <SpeedDialAction
                key={action.name}
                icon={action.icon}
                slotProps={{
                    tooltip: {
                        open: true,
                        placement: 'left-end',
                        title: <Typography sx={{width: '150px', textAlign: 'right'}}>{action.name}</Typography>,
                    },
                }}
                onClick={handleClose}
            />
            ))}
        </SpeedDial>
        </>
    );

}