'use client';
import ErrorIcon from '@mui/icons-material/Error';
import { useBaselineColumnStats, useFilteredColumnStats } from '@/hooks/useColumnStats';

import MyTreeMap from '@/components/elements/MyTreeMap';

import MyBarChart from '@/components/elements/MyBarChart';
import EnumFilter from '@/components/filters/EnumFilter';
import HistogramFilter from '@/components/filters/HistogramFilter';
import TextFilter from '@/components/filters/TextFilter';
import React from 'react';
import MiniTextFilter from '@/components/filters/MiniTextFilter';
import { MyTooltip } from '@/components/elements/displays';
import CellWithDistribution from './CellWithDistribution';
import CellWithQuickFilter from './CellWithQuickFilter';
import { useSingleColumnPreference } from '@/hooks/useColumnPreference';
import { useSingleColumnMeta } from '@/hooks/useApiData';
import { RENDER_TYPES } from '@/constants';
import useSetting from '@/hooks/useSetting';
import { Box, Typography, CircularProgress } from '@mui/material';


const TextCell = React.memo(function TextCell({text, height}) {

  return (
    <Box sx={{
      display: 'flex', alignItems: 'center', justifyContent: 'center', width: '100%', height,
      }}>
      <Typography variant="body1">{text}</Typography>
    </Box>
  )
});

const ColumnStatsCell = React.memo(({ name }) => {
  const { value: height} = useSetting('heightOfColumnDistribution');

  const colPref = useSingleColumnPreference(name);
  const colMeta = useSingleColumnMeta(name);
  const baselineColumnStats = useBaselineColumnStats(name);
  const filteredColumnStats = useFilteredColumnStats(name);

  const data = filteredColumnStats?.data;



  if (colMeta?.allNull) {
    return <TextCell text="All NULL" height={height} />;
  }

  if (baselineColumnStats.loading || filteredColumnStats.loading) {
    return <CircularProgress size={height} />
  }

  if (baselineColumnStats.error || filteredColumnStats.error) {
    return <MyTooltip title={baselineColumnStats.error || filteredColumnStats.error}><ErrorIcon color="error" /></MyTooltip>;
  }



  switch (colPref?.renderType) {
    case RENDER_TYPES.NUMBER:
      return (
        <CellWithDistribution
          name={name}
          data={data}
          miniVisual={<MyBarChart columnStats={data} size={height} />}
          mainVisual={<HistogramFilter name={name}/>}
        />
      )
    case RENDER_TYPES.CATEGORY:
      return (
        <CellWithDistribution
          name={name}
          data={data}
          miniVisual={<MyTreeMap columnStats={data} size={height}/>}
          mainVisual={<EnumFilter name={name}/>}
          cardHeight={600}
        />
      )
    case RENDER_TYPES.TEXT:
      return (
        <CellWithQuickFilter
          name={name}
          quickFilter={<MiniTextFilter name={name}/>}
          mainFilter={<TextFilter name={name} data={data}/>}
        />
      )
    default:
      return (
        <TextCell text={colPref?.renderType} height={height} />
      )
  }
});

ColumnStatsCell.displayName = 'ColumnStatsCell';

export default ColumnStatsCell;