import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useAllColumnsMeta } from '@/hooks/useApiData';
import { isMissing } from '@/utils/helpers';
import { setColumnDefs } from '@/store/agColDefsSlice';
import useMUITheme from '@/hooks/useMUITheme';
import AGCell from '@/components/table/AGCell';
import useSetting from '@/hooks/useSetting';
import _ from 'lodash';
import { useAllRenderTypes } from '@/hooks/useColumnPreference';
import { RENDER_TYPES } from '@/constants';


export const useColumnDefs = () => {
  const dispatch = useDispatch();
  const { data, loading, error } = useAllColumnsMeta() || {};
  const columnDefs = useSelector((state) => state.agColDefs.columnDefs);
  const derivedColumns = useSelector((state) => state.derivedColumns);
  const shouldInit = !isMissing(data) && (isMissing(columnDefs) || _.isEmpty(columnDefs));

  useEffect(() => {
    if (shouldInit) {
      // Initialize columnDefs
      const { initialOrder, meta } = data;

      const initialColumnDefs = initialOrder.map(name => ({
        field: name, pinned: null, hide: false, cellDataType: meta[name].agType,
      }));
      const derivedColumnDefs = _.map(derivedColumns, (col) => ({
        field: col.name, pinned: null, hide: false, cellDataType: 'text',
      }));
      const allColumnDefs = initialColumnDefs.concat(derivedColumnDefs);
      dispatch(setColumnDefs(allColumnDefs));

    }
  }, [dispatch, shouldInit, data, derivedColumns]);

  return { columnDefs, loading, error };
};

export const useSingleColumnDef = (columnName) => {
  const {columnDefs} = useColumnDefs();
  return columnDefs.find(def => def.field === columnName);
};



export const useDefaultColDef = () => {
  const {value: defaultColumnWidth} = useSetting('tableDefaultColumnWidth');
  return  {
    sortable: true,
    filter: false,
    resizable: true,
    minWidth: 80,
    autoHeight: true,
    width: defaultColumnWidth,
    maxWidth: 800,
    unSortIcon: true,
    floatingFilter: false,
    cellStyle: {
      display: 'flex',
      alignItems: 'center', // Vertically center the text
      paddingBottom: '0px', // Remove bottom padding
    },
  };
};

const headerTemplate = (muiTheme) => {
  return `<div class="ag-cell-label-container" 
              role="presentation" 
              style="color: ${muiTheme.palette.primary.light}; background-color: ${muiTheme.palette.background.card}; font-size: ${muiTheme.typography.fontSize}px;">
          <span style="flex: 1;"></span>
          <div data-ref="eLabel" 
               class="ag-header-cell-label" 
               role="presentation" 
               style="padding: 4px; max-width: 30px; margin-right: 15px;">
            <span data-ref="eSortOrder" class="ag-header-icon ag-sort-order"></span>
            <span data-ref="eSortAsc" class="ag-header-icon ag-sort-ascending-icon" style="color: ${muiTheme.palette.primary.main};"></span>
            <span data-ref="eSortDesc" class="ag-header-icon ag-sort-descending-icon" style="color: ${muiTheme.palette.primary.main};"></span>
            <span data-ref="eSortNone" class="ag-header-icon ag-sort-none-icon" style="color: ${muiTheme.palette.text.tertiary};"></span>
            
          </div>
            
          <span data-ref="eText" 
                class="ag-header-cell-text header-button" 
                style="cursor: move; margin-left: 15px;"
                role="columnheader"></span>
        </div>`;
}



const cellStyle = (muiTheme) => {
  return params => {
    const isPinned = ['left', 'right'].includes(params.column.getPinned());
    const usePadding = params.colDef.cellDataType === 'boolean' && !params.data?.isTopRow;
    return {
      backgroundColor: isPinned ? muiTheme.palette.background.pinned : muiTheme.palette.background.paper,
      padding: usePadding ? '8px' : '0px',
    }
  }
};

export const useExpandedColumnDefs = () => {
  const {columnDefs} = useColumnDefs();
  const muiTheme = useMUITheme();
  const allRenderTypes = useAllRenderTypes();
  const { value: tableMaxRowHeight} = useSetting('tableMaxRowHeight');

  if (_.isEmpty(allRenderTypes) || _.isEmpty(columnDefs)) {
    return [];
  }

  const expandedColumnDefs = (columnDefs || []).map(col => {
    const renderType = allRenderTypes[col.field];
    const d = {
        ...col,
        headerName: col.field,
        cellRendererSelector: (params) => ({
            component: (col.cellDataType === 'boolean' && !params.data?.isTopRow) ? 'agCheckboxCellRenderer' : AGCell,
            componentParams: {
              name: col.field,
            },
        }),
        cellRendererParams: {
          name: col.field,
        },
        cellStyle: cellStyle(muiTheme),
        headerComponentParams : {
          template: headerTemplate(muiTheme),
        }
      };
    if ([RENDER_TYPES.IFRAME, RENDER_TYPES.IMAGE_URL, RENDER_TYPES.VIDEO_URL].includes(renderType)) {
      d.width = tableMaxRowHeight;
      d.sortable = false;
      d.resizable = false;
    }

    return d;

    }
  );

  return expandedColumnDefs;
};