import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { fetchRowData } from '@/store/rowDataSlice';
import { initialState } from '@/store/dataSlice';
import { isMissing } from '@/utils/helpers';
import { fetchBoxPlotData } from '@/store/boxPlotSlice';
import { fetchBubblePlotData } from '@/store/bubblePlotSlice';
import { fetchColumnMeta } from '@/store/columnMetaSlice';
import { fetchHeatMap } from '@/store/heatMapSlice';
import { fetchHistogramData } from '@/store/histogramSlice';
import { useColumnDefs } from '@/hooks/useColumnDefs';
import { initSettingsSlice } from '@/store/dataSlice';
import { updateSettings } from '@/store/settingsSlice';
import { useFilteredColumnStats } from '@/hooks/useColumnStats';
import { useDerivedColumnMeta } from '@/hooks/useDrivedColumn';
import { API_PREFIX } from '@/utils/urls';

const useApiData = ({selector, action, shouldWait}) => {
  const dispatch = useDispatch();

  const { data, loading, error, shouldRefresh } = useSelector(selector) || initialState;
  const dataIsNull = (data === null);

  useEffect(() => {
    if (!shouldWait && (dataIsNull || shouldRefresh)) {
      dispatch(action);
    }
  }, [dispatch, dataIsNull, shouldRefresh, action, shouldWait]);

  return { data, loading, error, dataIsNull };
};

export const useAllColumnsMeta = () => {
  const selector = state => state.columnMeta;
  const action = fetchColumnMeta();
  return useApiData({selector, action});
};

export const useSingleColumnMeta = (columnName) => {
  const { data, loading, error } = useAllColumnsMeta();

  const dC = useDerivedColumnMeta(columnName);

  if (dC) {
    return dC;
  } else {
    return data?.meta[columnName];
  }
};


export const useRowData = () => {
  const { columnDefs } = useColumnDefs();
  const selector = state => state.rowData;
  const action = fetchRowData();
  return useApiData({selector, action, shouldWait: columnDefs.length === 0});
};

export const useBoxPlotData = () => {
  const selector = state => state.boxPlot;
  const boxPlotColumns = useSelector(state => state.settings.boxPlotColumns) || [];
  const shouldWait = boxPlotColumns.length === 0;
  const action = fetchBoxPlotData({boxPlotColumns});
  return useApiData({selector, action, shouldWait});
};

export const useBubblePlotData = () => {
  const selector = state => state.bubblePlot;
  const bubblePlotXColumn = useSelector(state => state.settings.bubblePlotXColumn);
  const bubblePlotYColumn = useSelector(state => state.settings.bubblePlotYColumn);
  const { data: xStatsData} = useFilteredColumnStats(bubblePlotXColumn);
  const xBin = xStatsData?.bin;
  const { data: yStatsData} = useFilteredColumnStats(bubblePlotYColumn);
  const yBin = yStatsData?.bin;
  const shouldWait = isMissing(bubblePlotXColumn) || isMissing(bubblePlotYColumn) || isMissing(xBin) || isMissing(yBin);
  const action = fetchBubblePlotData({bubblePlotXColumn, bubblePlotYColumn, xBin, yBin});
  return useApiData({selector, action, shouldWait});
};

export const useHeatMapData = () => {
  const selector = state => state.heatMap;
  const heatmapXColumn = useSelector(state => state.settings.heatmapXColumn);
  const heatmapYColumn = useSelector(state => state.settings.heatmapYColumn);

  const shouldWait = isMissing(heatmapXColumn) || isMissing(heatmapYColumn);
  const action = fetchHeatMap({heatmapXColumn, heatmapYColumn});
  return useApiData({selector, action, shouldWait});
};

export const useHistogramData = () => {
  const selector = state => state.histogram;

  const histogramColumn = useSelector(state => state.settings.histogramColumn);
  const { data: histogramStatsData} = useFilteredColumnStats(histogramColumn);
  const bin = histogramStatsData?.bin;
  const shouldWait = isMissing(histogramColumn) || isMissing(bin);
  const action = fetchHistogramData({histogramColumn, bin});
  return useApiData({selector, action, shouldWait});
};

export const useInitSettings = () => {
  const selector = state => state.initSettings;

  const action = async (dispatch, getState) => {
    const { data, loading, shouldRefresh } = getState().initSettings || initialState;
    if ((data || loading) && !shouldRefresh) return; //  Already loading or loaded  skip!

    dispatch(initSettingsSlice.actions.fetchStart());
    try {
        const response = await fetch(`${API_PREFIX}/settings`);
        const settings = await response.json();
        dispatch(initSettingsSlice.actions.fetchSuccess(settings));
        dispatch(updateSettings(settings));

    } catch (err) {
      console.error(err);
      dispatch(initSettingsSlice.actions.fetchError(err.message));
    }

  };
  return useApiData({selector, action});
};