import SettingsSchema from '@/settings.schema.yaml';
import { useInitSettings } from '@/hooks/useApiData';
import { isMissing } from '@/utils/helpers';
import { useSelector } from 'react-redux';

const useSetting = (settingKey) => {
    const {data: initSettings} = useInitSettings();
    const schema = SettingsSchema.properties[settingKey];
    const value = useSelector((state) => state.settings[settingKey]);

    if (isMissing(initSettings)) {
        return {};
    }

    if (isMissing(schema)) {
        throw new Error(`No schema record for ${settingKey}. There may be a typo.`);
    }
    if (isMissing(value)) {
        throw new Error(`The developer forgot to set the default value for ${settingKey} in settingsSlice.js or Python settings.py`);
    }

    return {schema, initValue: initSettings[settingKey], value};
}

export default useSetting;