
import { Box, Typography } from '@mui/material';
import MyBarChart from '@/components/elements/MyBarChart';
import { useSelector } from 'react-redux';
import { useState, useCallback, useMemo } from 'react';
import { MyNumberField } from '@/components/elements/Controls';
import { InfoCard } from '@/components/elements/ClickCard';
import StatsCount from '@/components/table/ColumnStats/StatsCount';
import NullFilterRadio from './NullFilterRadio';
import { BorderedBox } from '@/components/elements/displays';
import { useFilteredColumnStats } from '@/hooks/useColumnStats';
import { TextWithAddon } from '@/components/elements/displays';
import FilterCardBottomButtons from './FilterCardBottomButtons';
import { setColumnFilterAndRefreshData } from '@/store/agColDefsSlice';
import { useDispatch } from 'react-redux';
import React from 'react';
import { useSingleColumnMeta } from '@/hooks/useApiData';

const HistogramFilter = React.memo(({ name }) => {
  const dispatch = useDispatch();
  const filteredColumnStats = useFilteredColumnStats(name);
  const data = filteredColumnStats.data;
  const meta = useSingleColumnMeta(name);
  const { data: statsData} = useFilteredColumnStats(name) || {};
  const reduxRange = useSelector(state => state.agColDefs.filters[name])?.range;
  const reduxFilterNull = useSelector(state => state.agColDefs.filters[name])?.null;

  const defaultRange = useMemo(() => [
    reduxRange?.[0] ?? statsData?.bin?.min,
    reduxRange?.[1] ?? statsData?.bin?.max
  ], [reduxRange, statsData?.bin?.min, statsData?.bin?.max]);

  const initialBufferFilters = useMemo(() => ({
    range: defaultRange,
    null: reduxFilterNull || 'include',
  }), [defaultRange, reduxFilterNull]);

  const [bufferFilters, setBufferFilters] = useState(initialBufferFilters);

  // This is a trick to force the bar chart to re-render when the brush range changes
  const [keyBarChart, setKeyBarChart] = useState(0);


  const onRangeChange = ({startValue, endValue}) => {
    setBufferFilters({null: 'exclude', range: [parseFloat(startValue), parseFloat(endValue)]});
  }
  const setLowerBound = (value) => {
    setBufferFilters({null: 'exclude', range: [parseFloat(value), bufferFilters.range[1]]});
  }
  const setUpperBound = (value) => {
    setBufferFilters({null: 'exclude', range: [bufferFilters.range[0], parseFloat(value)]});
  }
  const setFilterNull = (value) => {
    setBufferFilters({...bufferFilters, null: value});
  }

  const applyFilters = () => {
    const filter = {
      filterType: 'myRange',
      range: bufferFilters.range,
      null: !meta.hasNull ? 'N/A' : bufferFilters.null,
    };
    setColumnFilterAndRefreshData(dispatch, name, filter);
  }

  const onCancelChanges = useCallback(() => {
    setBufferFilters(initialBufferFilters);
    setKeyBarChart(keyBarChart + 1);
  }, [initialBufferFilters, keyBarChart]);


  const rangeMinWarning = (
    <InfoCard title="Range Min is inclusive">
      <Typography>
        In the histogram above, lower bound of each bin is NOT inclusive.
      </Typography>
      <Typography>
        {'When we translate the visual brush to SQL, the lower bound becomes inclusive, '}
        {'in order to match people\'s intuition.'}
      </Typography>
    </InfoCard>
  );



  return (
    <Box sx={{height: '100%', display: 'flex', flexDirection: 'column', gap: 1}}>
        <BorderedBox sx={{display: 'flex', flexDirection: 'column', gap: 1}}>
          <StatsCount columnName={name}/>
          {!meta.hasNull ? null : <NullFilterRadio filterNull={bufferFilters.null} setFilterNull={setFilterNull}/>}
        </BorderedBox>


        <MyBarChart key={keyBarChart} columnStats={data} size={150} showBrush={true} onRangeChange={onRangeChange}/>

        <Box sx={{display: 'flex', gap: 2, mt: 2}}>
          <MyNumberField
            label={<TextWithAddon text="Range Min (Inclusive)" addon={rangeMinWarning}/>}
            value={bufferFilters.range[0]} onValueChange={setLowerBound}/>
          <MyNumberField label="Range Max (Inclusive)" value={bufferFilters.range[1]} onValueChange={setUpperBound}/>
        </Box>
        <FilterCardBottomButtons columnName={name} cancelChanges={onCancelChanges} applyFilters={applyFilters} />

      </Box>


 );
});

HistogramFilter.displayName = 'HistogramFilter';

export default HistogramFilter;