"use client";

import React, { useEffect } from 'react';
import FolderNav from '@/components/folderBrowser/FolderNav';
import FolderItemPreview from '@/components/folderBrowser/Preview';
import { useSearchParams } from 'next/navigation';
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import { updateSettings } from '@/store/settingsSlice';
import { ROUTES } from '@/constants';
import HorizontalSplitBox from '@/components/layout/HorizontalSplitBox';
import { pathBasename } from '@/store/folderBrowserSlice';
import { updateChildren } from '@/store/folderBrowserSlice';
import useSetting from '@/hooks/useSetting';

const FolderBrowser = React.memo(function FolderBrowser() {
  const dispatch = useDispatch();
  const {value: defaultRootFolder} = useSetting('folderBrowserDefaultRootFolder');
  const reduxRootFolder = useSelector(state => state.settings.folderBrowserRootFolder);
  const searchParams = useSearchParams();
  const userRequestedRootFolder = searchParams.get('rootFolder');
  const hasTreeData = useSelector(state => state.folderBrowser.treeData.length > 0);

  // Update document title based on rootFolder
  useEffect(() => {
    if (userRequestedRootFolder) {
      const title = pathBasename(userRequestedRootFolder);
      document.title = title;
    }
  }, [userRequestedRootFolder]);

  useEffect(() => {
    const rootFolder = userRequestedRootFolder || defaultRootFolder;
    if (!reduxRootFolder && rootFolder) {
      dispatch(updateSettings({folderBrowserRootFolder: rootFolder}));
      dispatch(updateChildren(''));
    }
  }, [userRequestedRootFolder, dispatch, reduxRootFolder, defaultRootFolder]);

  useEffect(() => {
    if (!hasTreeData) {
      dispatch(updateChildren(''));
    }
  }, [dispatch, hasTreeData]);


  return (
    <HorizontalSplitBox
      left={<FolderNav />}
      right={<FolderItemPreview/>}
      initLeftWidth='30%'
    />
  );
});

FolderBrowser.displayName = ROUTES.FolderBrowser;

export default FolderBrowser;