import { useSelector } from 'react-redux';
import { createTheme } from '@mui/material/styles';


const useMUITheme = () => {
  const mode = useSelector((state) => state.settings.mode) || 'light';
  const baseFontSize = useSelector((state) => state.settings.baseFontSize);
  const isDarkMode = mode === 'dark';

  return createTheme({
    typography: {
      fontSize: baseFontSize,
    },
    palette: {
      mode: mode,
      primary: {
        main: '#1976d2',
        light: '#42a5f5',
        dark: '#1565c0',
        contrastText: '#fff'
      },
      highlight: {
        main: '#E3D026',
        light: '#E9DB5D',
        dark: '#A29415',
        contrastText: '#242105',
      },
      plotFill: isDarkMode ? 'SteelBlue' : 'SkyBlue',
      text: {
        primary: isDarkMode ? '#E0E0E0' : '#212121',
        secondary: isDarkMode ? '#A0A0A0' : '#5F6368',
        tertiary: isDarkMode ? '#4C4C4E' : '#B6B9BD',
      },
      borderColor: isDarkMode ? '#4C4C4E' : '#B6B9BD',
      background: {
        paper: isDarkMode ? '#000000' : '#FFFFFF',
        card: isDarkMode ? '#1C1C1E' : 'Snow',
        pinned: isDarkMode ? '#2A2E3A' : 'LightGray',
        justClicked: isDarkMode ? '#3A3A26' : 'LightYellow',
      },
    },
  });
}

export default useMUITheme;