"use client";
import { Box } from '@mui/material';
import { ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import { useEffect, useState } from 'react';
import useMUITheme from "@/hooks/useMUITheme";
import useLeavePageConfirmation from '@/hooks/useLeavePageConfirmation';
import { HEIGHT_BOTTOM_BAR } from '@/constants';
import TopSpeedDial from '@/components/TopSpeedDial';
import useSetting from '@/hooks/useSetting';
import Toast from '@/components/Toast';

const TopLevelPage = ({children, bottomBar}) => {

  const [mounted, setMounted] = useState(false);
  const muiTheme = useMUITheme();
  const { value: enableDebugging } = useSetting('enableDebugging');

  useLeavePageConfirmation(!enableDebugging); // Confirm before leaving page

  useEffect(() => {
    setMounted(true);

  }, []);

  if (!mounted) {
    return null; // or a loading state
  }



  return (
    <ThemeProvider theme={muiTheme}>
      <CssBaseline />
      <Box sx={{ width: '100%', height: '100vh', display: 'flex', flexDirection: 'column' }}>
        <Box sx={{ flex: 1, height: `calc(100% - ${HEIGHT_BOTTOM_BAR}px)`}}>
          {children}
        </Box>
        <Box sx={{flex: 0}}>
          {bottomBar}
        </Box>
      </Box>
      <TopSpeedDial />
      <Toast />
    </ThemeProvider>
  );
};

export default TopLevelPage;