import { useMemo } from 'react';
import { AgGridReact } from 'ag-grid-react';
import { AGGridResponsiveContainer } from '@/components/elements/displays';
import { useEffect, useRef, useCallback } from 'react';
import { COL_DEF_NUMBER, COL_DEF_PERCENT } from '@/utils/agGridUtils';
import { coalesce } from '@/utils/helpers';
import React from 'react';
import useAGGridTheme from '@/hooks/useAGGridTheme';
import useMUITheme from '@/hooks/useMUITheme';
import useFilters from '@/hooks/useFilters';
import { useSelector } from 'react-redux';
import { createSelector } from '@reduxjs/toolkit';
import { AllCommunityModule, ModuleRegistry } from 'ag-grid-community';
import { buildProgressBarRenderer } from '@/utils/agGridUtils';

// Register all Community features
ModuleRegistry.registerModules([AllCommunityModule]);


const ensureClickedValueIsVisible = ({clickedId, gridApi}) => {
    if (clickedId) {
        gridApi.forEachNode((node) => {
            if (node.data.value === clickedId) {
                gridApi.ensureNodeVisible(node);
            }
        });
    }
}

const updateSelection = ({including, gridApi}) => {
    gridApi.forEachNode((node) => {
        const targetValue = including.includes(node.data.value);
        const currentValue = node.isSelected();
        if (targetValue !== currentValue) {
            node.setSelected(targetValue);
        }
    });
};


const EnumFilterTable = React.memo(({
    name,
    bufferFilters,
    setBufferFilters
}) => {

    const rowData = useSelector(createSelector([
        state => state.baselineColumnStats[name]?.data,
        state => state.filteredColumnStats[name]?.data,
    ], (baselineData, filteredData) => {
        return baselineData.cnt_values.map(({value, cnt}) => {
            const filteredCnt = coalesce(filteredData.cnt_values.find((item) => item.value === value)?.cnt, 0);
            return {
                value,
                baseline_cnt: cnt,
                filtered_cnt: filteredCnt,
                percent: parseFloat((100 * filteredCnt / filteredData.cnt_all).toFixed(3)),
            };
        });
    }));
    const { including, clickedId } = bufferFilters;

    const { hasFilters } = useFilters();
    const gridRef = useRef();
    const agGridTheme = useAGGridTheme({});
    const muiTheme = useMUITheme();


    useEffect(() => {
        if (gridRef.current && gridRef.current.api) {
            const api = gridRef.current.api;
            if (clickedId) {
                ensureClickedValueIsVisible({clickedId, gridApi: api});
                api.forEachNode((node) => {
                    node.setSelected(node.data.value === clickedId);
                });
            }
        }
    }, [clickedId]);

    useEffect(() => {
        if (gridRef.current && gridRef.current.api) {
            updateSelection({including, gridApi: gridRef.current.api});
        }
    }, [including]);



    const colDefs = useMemo(() => [
        {
            headerName: 'Value',
            field: 'value',
            filter: 'agTextColumnFilter',
        },
        {
            headerName: 'Percent',
            field: 'percent',
            ...COL_DEF_PERCENT(muiTheme),
            cellRenderer: buildProgressBarRenderer({min: 0, max: 100, isPercent: true, muiTheme}),
        },
        {
            headerName: hasFilters ? 'Filtered Count' : 'Count',
            field: 'filtered_cnt',
            ...COL_DEF_NUMBER(muiTheme),
        },
        {
            headerName: 'Baseline Count',
            field: 'baseline_cnt',
            ...COL_DEF_NUMBER(muiTheme),
            hide: !hasFilters,
        },

        {
            headerName: 'Filtered / Baseline',
            valueGetter: (params) => {
                if (params.data.baseline_cnt === 0) {
                    return 0;
                }
                return (100 * params.data.filtered_cnt / params.data.baseline_cnt).toFixed(1);
            },
            cellRenderer: buildProgressBarRenderer({min: 0, max: 100, isPercent: true, muiTheme}),
            ...COL_DEF_PERCENT(muiTheme),
            hide: !hasFilters,
        },
    ], [hasFilters, muiTheme]);

    const defaultColDef = useMemo(() => ({
        sortable: true,
        filter: false,
        resizable: true,
        flex: 1,

      }), []);




    const onSelectionChanged = useCallback((params) => {
        if (params.source === 'api') {
            return; // No need to update the including array
        }
        const selectedNodes = params.api.getSelectedNodes();
        if (selectedNodes.length > 1) {
            setBufferFilters({...bufferFilters, clickedId: null});
        }
        const newIncluding = [];
        params.api.forEachNode((node) => {
            if (node.isSelected()) {
                newIncluding.push(node.data.value);
            }
        });
        setBufferFilters({...bufferFilters, including: newIncluding});
    }, [setBufferFilters, bufferFilters]);


    const getRowId = useCallback((params) => String(params.data.value), []);

    const getRowStyle = useCallback((params) => {
        if (params.data.value === clickedId) {
            return {
                color: muiTheme.palette.primary.light,
            };
        } else {
            return {};
        }
    }, [clickedId, muiTheme]);


    const onGridReady = useCallback((params) => {
        updateSelection({including, gridApi: params.api});
    }, [including]);

    return (
        <AGGridResponsiveContainer>
            <AgGridReact
                domLayout='normal'
                ref={gridRef}
                getRowId={getRowId}
                theme={agGridTheme}
                rowSelection={{
                    mode: 'multiRow',
                    headerCheckboxSelection: true,
                    checkboxSelection: true,
                }}
                selectionColumnDef={{
                   sortable: true,
                   pinned: 'left',
                }}
                onSelectionChanged={onSelectionChanged}
                rowData={rowData}
                columnDefs={colDefs}
                defaultColDef={defaultColDef}
                getRowStyle={getRowStyle}
                onGridReady={onGridReady}
            />
        </AGGridResponsiveContainer>
    );
});

EnumFilterTable.displayName = 'EnumFilterTable';
export default EnumFilterTable;
