import { useDispatch } from 'react-redux';
import { updateSettings } from '@/store/settingsSlice';
import { isMissing } from '@/utils/helpers';
import React from 'react';
import { Box, CircularProgress, Slider, Typography } from '@mui/material';
import InfoOutlineIcon from '@mui/icons-material/InfoOutline';
import { MyTooltip } from '@/components/elements/displays';
import useSetting from '@/hooks/useSetting';

const SliderInSettings = React.memo(({settingKey, extraChange=null, horizontal=false}) => {
    const dispatch = useDispatch();
    const {schema, initValue, value} = useSetting(settingKey);

    if (isMissing(initValue)) {
        return <CircularProgress />;
    }

    const setValue = (newValue) => {
        dispatch(updateSettings({[settingKey]: newValue}));
        if (extraChange) {
            extraChange(newValue);
        }
    }

    const infoIcon = (
        <MyTooltip title={settingKey}>
            <InfoOutlineIcon fontSize="small"/>
        </MyTooltip>
    )

    return (
        <Box sx={{
            display: 'flex',
            flexDirection: horizontal ? 'row' : 'column',
            alignItems: 'center',
            gap: horizontal ? 1 : 0,
        }}>

            <Typography variant="body2" sx={{
                color: 'text.secondary', textAlign: 'left',
                width: (horizontal ? 'auto' : '100%')
            }} gutterBottom>
              {infoIcon} {schema.description}: {value}
            </Typography>
            <Box sx={{ width: (horizontal ? '100px' : '100%')}}>
                <Slider
                    value={value}
                    onChange={(e, newValue) => {
                        setValue(newValue);
                        e.stopPropagation();
                    }}
                    min={schema.minimum}
                    max={schema.maximum}
                    size="small"
                    step={schema.step || 1}
                    valueLabelDisplay={horizontal ? 'off' : 'auto'}
                    sx={{ width: '100%' }}
                />
            </Box>
        </Box>

    );
});

SliderInSettings.displayName = 'SliderInSettings';

export default SliderInSettings;
