'use client';

import { ImageBoxCell } from '@/components/table/rightClickMenu/ImageBoxCell';

import React from 'react';
import ExpandableTextCell from '@/components/table/ExpandableTextCell';
import { RENDER_TYPES } from '@/constants';
import { IFrameVisualizationCell } from '@/components/table/rightClickMenu/IFrameVisualizationCell';
import { useSingleColumnPreference } from '@/hooks/useColumnPreference';
import ColumnStatsCell from '@/components/table/ColumnStats/ColumnStatsCell';
import { JsonBoxCell } from '@/components/table/rightClickMenu/JsonBoxCell';


const AGCell = React.memo((props) => {

  const { name, value, colDef } = props;
  const colPref = useSingleColumnPreference(name);
  const renderType = colPref?.renderType;

  if (props.data?.isTopRow) {
    return <ColumnStatsCell name={name}/>;
  } else {
    switch (renderType) {
      case RENDER_TYPES.IMAGE_URL:
        return <ImageBoxCell name={name} value={value} colPref={colPref}/>;
      case RENDER_TYPES.JSON:
        return <JsonBoxCell name={name} value={value} colDef={colDef} />;
      case RENDER_TYPES.IFRAME:
        return <IFrameVisualizationCell name={name} value={value} colDef={colDef}/>;
      default:
        return <ExpandableTextCell columnName={name} text={String(value)} colDef={colDef}/>;

    }
  }

});

AGCell.displayName = 'AGCell';

export default AGCell;