import { IconButton } from "@mui/material";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { updateSettings } from "@/store/settingsSlice";
import { SX_TEXT_HOVER } from "@/utils/styles";
import DarkModeOutlinedIcon from '@mui/icons-material/DarkModeOutlined';
import LightModeOutlinedIcon from '@mui/icons-material/LightModeOutlined';


export const IconColorMode = React.memo(function IconColorMode() {
    const dispatch = useDispatch();
    const mode = useSelector(state => state.settings.mode);

    const icon = mode === 'light' ? <LightModeOutlinedIcon/> : <DarkModeOutlinedIcon/>;

    const handleClick = () => {
        dispatch(updateSettings({mode: mode === 'light' ? 'dark' : 'light'}));

    }

    return (
      <IconButton size="small"
        onClick={handleClick}
        sx={{
          ...SX_TEXT_HOVER
        }}
        >
          {React.cloneElement(icon, {fontSize: 'small'})}
        </IconButton>
    );
  });