import React from 'react';
import { Box, Typography } from '@mui/material';
import { useSelector } from 'react-redux';
import { getFileUrl } from '@/utils/urls';
import { pathBasename } from '@/store/folderBrowserSlice';

const ViewAlbum = React.memo(function ViewAlbum({photoList}) {
  const imageSize = useSelector(state => state.settings.folderBrowserImageSize);
  const cropSquare = useSelector(state => state.settings.folderBrowserImageCropSquare);

    if (!photoList) {
        return null;
    }

    return (
      <Box sx={{display: 'flex', flexDirection: 'column', gap: 2}}>

      <Box sx={{
        display: 'flex', flexWrap: 'wrap', gap: 0.5, flex: 1,
        alignItems: 'flex-start'
      }} >
        {photoList.map((src, i) => (
          <Box
            key={i}
            sx={{position: 'relative', height: imageSize, display: 'inline-block'}}
          >
          <Box
            component="img"
            src={getFileUrl(src)}
            alt={pathBasename(src)}
            loading="lazy"
            sx={{
              height: imageSize,
              width: cropSquare ? imageSize : 'auto',
              objectFit: cropSquare ? 'cover' : 'contain'
            }}
          />

            <Box
              sx={{
                position: 'absolute', bottom: 0, color: 'text.primary',
                backgroundColor: 'rgba(0, 0, 0, 0.5)', width: '100%', padding: 0.5,
              }}
            >
              <Typography variant="body2" className='truncate'>
                  {pathBasename(src)}
              </Typography>
            </Box>

        </Box>
        ))}
      </Box>
      </Box>
    );
});

export default ViewAlbum;