import { FormControl, FormLabel, RadioGroup, Radio, FormControlLabel } from '@mui/material';
import React from 'react';


const NullFilterRadio = React.memo(({filterNull, setFilterNull}) => {
    const options = [
        {label: 'Include', value: 'include'},
        {label: 'Exclude', value: 'exclude'},
        {label: 'Only NULL', value: 'only'}
      ];

    return (
        <FormControl sx={{display: 'flex', flexDirection: 'row', alignItems: 'center', gap: 2}}>
            <FormLabel>How to filter NULL values?</FormLabel>
            <RadioGroup
                row
                value={filterNull}
                onChange={(e) => setFilterNull(e.target.value)}
            >
            {options.map(({label, value}) => (
                <FormControlLabel key={value} value={value} control={<Radio size="small" />} label={label} />
            ))}
            </RadioGroup>
      </FormControl>
    )
});

NullFilterRadio.displayName = 'NullFilterRadio';

export default NullFilterRadio;