'use client';

import { useState } from 'react';
import { Box, Popover } from '@mui/material';
import React from 'react';
import { SX_CARD } from '@/utils/styles';


const CellRightClickCard = React.memo(function CellRightClickCard({children, sx}) {
  const [menuOpen, setMenuOpen] = useState(false);
  const [menuAnchorEl, setMenuAnchorEl] = useState(null);

  const handleOpenContextMenu = (event) => {
    event.preventDefault();
    setMenuAnchorEl(event.currentTarget);
    setMenuOpen(true);
  };

  const handleCloseContextMenu = () => {
    setMenuOpen(false);
    setMenuAnchorEl(null);
  };

  return (
          <Box onContextMenu={handleOpenContextMenu} sx={{
            position: 'absolute',
            right: 0,
            top: 0,
            width: '100%',
            height: '100%',
            backgroundColor: 'rgba(0, 0, 0, 0)',
          }}>
            {/* A transparent box taking full height to capture right click event */}
            <Popover
                open={menuOpen}
                anchorEl={menuAnchorEl}
                onClose={handleCloseContextMenu}
                anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
                }}
            >
              <Box sx={{
                  ...SX_CARD,
                  display: 'flex', flexDirection: 'column', gap: 1,
                  ...sx, padding: 1
              }}>

                  {children}
              </Box>
            </Popover>
          </Box>

        )

});


export default CellRightClickCard;