import { ChipList } from '@/components/elements/displays';
import { FilteredCount } from '@/components/filters/FilteredCounts';
import NullPie from './NullPie';
import { Box } from '@mui/material';
import React from 'react';
import { useSingleColumnMeta } from '@/hooks/useApiData';
import { useBaselineColumnStats, useFilteredColumnStats } from '@/hooks/useColumnStats';

const StatsCount = React.memo(({ columnName }) => {
  const meta = useSingleColumnMeta(columnName);
  const { data: baseData } = useBaselineColumnStats(columnName) || {};
  const { data: filteredData } = useFilteredColumnStats(columnName) || {};


  if (!baseData || !filteredData) {
    return null;
  }

  const items = meta.hasNull ? [
    <FilteredCount label="null" key='count-null'
                   countBaseline={100 * baseData.cnt_null / baseData.cnt_all}
                   countFiltered={100 * filteredData.cnt_null / filteredData.cnt_all}
                   addColor={true}
                   isPercentage={true}
                   />,
    <FilteredCount label="Not null" key='count-not-null'
                   countBaseline={100 * (baseData.cnt_all - baseData.cnt_null) / baseData.cnt_all}
                   countFiltered={100 * (filteredData.cnt_all - filteredData.cnt_null) / filteredData.cnt_all}
                   addColor={true}
                   isPercentage={true}
                   />,
  ] : ['This column does not have null values'];

  return (
    <Box className='counts' sx={{
      display: 'flex', flexDirection: 'row', gap: 1, alignItems: 'center', justifyContent: 'flex-start'
      }}>
        <NullPie columnName={columnName}/>
        <ChipList items={items} />
    </Box>
  );
});

StatsCount.displayName = 'StatsCount';

export default StatsCount;