import React, { useState } from 'react';
import { Box } from '@mui/material';
import { useSelector, useDispatch } from 'react-redux';
import Typography from '@mui/material/Typography';
import { TreeItem } from '@mui/x-tree-view/TreeItem';
import FolderOutlinedIcon from '@mui/icons-material/FolderOutlined';
import { timeAgo, formatTime } from '@/utils/time';
import { filesize } from 'filesize';
import { isMissing } from '@/utils/helpers';
import { SX_TEXT_HOVER, SX_WIDTH_100 } from '@/utils/styles';
import { useTreeItemModel } from '@mui/x-tree-view/hooks';
import { toggleItemExpansion } from '@/store/folderBrowserSlice';
import { OpenFolderInNewTabButton, OpenTableInNewTabButton } from '@/components/folderBrowser/elements';
import { RefreshFolderButton } from '@/components/folderBrowser/elements';
import { FloatingRightContainer } from '@/components/elements/containers';

const CustomLabel = React.memo(function CustomLabel({ children, className, isDir, lastModified, size, itemId }) {
    const showFileInfo = useSelector(state => state.settings.folderNavigationShowFileInfo);
    const [isHovered, setIsHovered] = useState(false);

    const getFileInfo = () => {
        switch (showFileInfo) {
            case 'size':
                return isMissing(size) ? '' : filesize(size);
            case 'lastModifiedRelative':
                return timeAgo(lastModified);
            case 'lastModifiedDateTime':
                return formatTime(lastModified);
            case 'none':
                return '';
            default:
                return '';
        }
    }

    const makeContent = () => {

        return isDir ? (
            <>
                <FolderOutlinedIcon fontSize="small"/>
                <Typography className='truncate'>
                    {children}
                </Typography>
                <FloatingRightContainer>
                    {isHovered && <OpenFolderInNewTabButton itemId={itemId} />}
                    {isHovered && <RefreshFolderButton itemId={itemId} />}
                </FloatingRightContainer>
            </>
        ) : (
            <>
               <Typography className='truncate'>
                    {children}
                </Typography>
                <Box sx={{flexGrow: 1}}></Box>
                <Typography color="text.tertiary" className='truncate' sx={{minWidth: '60px'}}>
                    {getFileInfo()}
                </Typography>
                <FloatingRightContainer>
                    {isHovered && <OpenTableInNewTabButton itemId={itemId} />}
                </FloatingRightContainer>
            </>
        )
    }

    return (
      <Box className={className} sx={{
        display: 'flex',
        alignItems: 'center',
        gap: 0.5,
        cursor: 'pointer',
        ...SX_TEXT_HOVER, ...SX_WIDTH_100,
      }}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      >
        {makeContent()}
      </Box>
    );
  });

export const CustomTreeItem = React.forwardRef(function CustomTreeItem(props, ref) {
    const item = useTreeItemModel(props.itemId);
    const dispatch = useDispatch();

    const handleDoubleClick = (e) => {
        e.stopPropagation();
        if (item.isDir) {
            dispatch(toggleItemExpansion(item.id));
        }
    }

    return (
        <TreeItem
            {...props}
            ref={ref}
            className="select-none"
            slots={{
                label: CustomLabel,
            }}
            onDoubleClick={handleDoubleClick}
            slotProps={{
                label: {
                    isDir: item.isDir,
                    lastModified: item.lastModified,
                    size: item.size,
                    itemId: item.id,
                },
            }}

        />
    );
});
