import { IconButton } from "@mui/material";
import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { updateSettings } from "@/store/settingsSlice";
import { SX_TEXT_HOVER } from "@/utils/styles";
import TextDecreaseOutlinedIcon from '@mui/icons-material/TextDecreaseOutlined';
import TextIncreaseOutlinedIcon from '@mui/icons-material/TextIncreaseOutlined';

const IconChangeFontSize = React.memo(function IconChangeFontSize({icon, change}) {
    const dispatch = useDispatch();
    const baseFontSize = useSelector(state => state.settings.baseFontSize);


    const handleClick = () => {
        const newFontSize = Math.min(Math.max(10, baseFontSize + change), 20);
        dispatch(updateSettings({baseFontSize: newFontSize}));

    }

    return (
      <IconButton size="small"
        onClick={handleClick}
        sx={{
          ...SX_TEXT_HOVER
        }}
        >
          {React.cloneElement(icon, {fontSize: 'small'})}
      </IconButton>
    );
});

export const IconFontSizeUp = React.memo(function IconFontSizeUp() {
    return <IconChangeFontSize icon={<TextIncreaseOutlinedIcon/>} change={1} />;
});

export const IconFontSizeDown = React.memo(function IconFontSizeDown() {
    return <IconChangeFontSize icon={<TextDecreaseOutlinedIcon/>} change={-1} />;
});