import { useSelector, useDispatch } from 'react-redux';
import { isMissing } from '@/utils/helpers';
import { runQuery } from '@/utils/api';
import { useEffect, useCallback } from 'react';
import { useSingleColumnMeta } from '@/hooks/useApiData';
import { sanitizeName } from '@/utils/sql/helpers';
import { cardinalitySlice } from '@/store/dataSlice';


const fetchCardinalityData = ({columnName, actions, sqlKey, query}) => async (dispatch, getState) => {
  const { data, loading, error, shouldRefresh } = getState().cardinality[columnName] || {};
  if ((data || loading) && !shouldRefresh) return; //  Already loading or loaded  skip!

  dispatch(actions.fetchStart({name: columnName}));

  try {
    const result = await runQuery({dispatch, key: sqlKey, query});
    dispatch(actions.fetchSuccess({name: columnName, ...result[0]}));
  } catch (err) {
    console.error(`error fetching cntD for ${columnName}`, err);
    dispatch(actions.fetchError({error: err.message, name: columnName}));
  }
};


export const useCardinality = (columnName) => {
  const dispatch = useDispatch();
  const actions = cardinalitySlice.actions;
  const meta = useSingleColumnMeta(columnName);
  const categoricalThreshold = useSelector(state => state.settings.thresholdCategorical);
  const tableExpr = useSelector(state => state.dataFile.tableExpr);
  const e = sanitizeName(columnName);

  const query = `
    SELECT approx_count_distinct(${e}) AS approxCntD,
    CASE
      WHEN approxCntD <= 1000 THEN COUNT(DISTINCT ${e})
      ELSE NULL
    END AS cntD,
    CASE WHEN approxCntD <= 1000 THEN 'low' ELSE 'high' END AS cardinality
    FROM ${tableExpr} WHERE ${e} IS NOT NULL
  `;

  const sqlKey = `cardinality-${columnName}`;
  const { data, loading, error, shouldRefresh } = useSelector(state => state.cardinality[columnName]) || {};

  const shouldInit = isMissing(data) && !loading && !error && !isMissing(query) && !isMissing(meta);

  const inferCardinality = useCallback(({cnt, cardinality}) => {
    dispatch(actions.fetchSuccess({name: columnName, approxCntD: cnt, cntD: cnt, cardinality}));
  }, [dispatch, actions, columnName]);

  useEffect(() => {
    if (shouldInit || shouldRefresh) {
      const { isBoolean, allNull, singleValue, isString, isInteger, maxCntDPerGroup, isPlainEncoding, isDerived } = meta;
      if (isDerived) {
        inferCardinality({cnt: null, cardinality: 'high'});
      } else if (allNull) {
        inferCardinality({cnt: 0, cardinality: 'low'});
      } else if (singleValue) {
        inferCardinality({cnt: 1, cardinality: 'low'});
      } else if (isBoolean) {
        inferCardinality({cnt: 2, cardinality: 'low'});
      } else if (maxCntDPerGroup > categoricalThreshold || isPlainEncoding) {
        // We know from metadata that this column cannot be low cardinality
        inferCardinality({cnt: null, cardinality: 'high'});
      } else if (isString || isInteger) {
        dispatch(fetchCardinalityData({columnName, actions, sqlKey, query}));
      } else {
        inferCardinality({cnt: null, cardinality: 'unknown'});
      }
    }
  }, [dispatch, shouldInit, shouldRefresh, columnName, query, actions, sqlKey, meta, categoricalThreshold, inferCardinality]);

   return {data, loading, error, sqlKey};
}
