import { useDispatch } from 'react-redux';
import { updateDef } from '@/store/agColDefsSlice';
import { BooleanIconButton } from '@/components/elements/Controls';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import React from 'react';

const ColumnHideIcon = React.memo(({columnName, hide}) => {
  const dispatch = useDispatch();
  const isHidden = hide === true;


  const toggleHide = () => {
    dispatch(updateDef({field: columnName, name: 'hide', value: isHidden ? null : true}));
  }


  return (
    <BooleanIconButton
      iconOff={<VisibilityOffIcon />}
      iconOn={<VisibilityIcon />}
      tooltipOn="Show column"
      tooltipOff="Hide column"
      value={isHidden}
      onClick={toggleHide}
    />
  );
});

ColumnHideIcon.displayName = 'ColumnHideIcon';

export default ColumnHideIcon;