import { Box } from '@mui/material';
import MyTreeMap from '@/components/elements/MyTreeMap';
import EnumFilterTable from './EnumFilterTable';
import { useState, useCallback, useMemo } from 'react';
import StatsCount from '@/components/table/ColumnStats/StatsCount';
import { useSelector, useDispatch } from 'react-redux';
import NullFilterRadio from './NullFilterRadio';
import { BorderedBox } from '@/components/elements/displays';
import FilterCardBottomButtons from './FilterCardBottomButtons';
import React from 'react';
import { useBaselineColumnStats, useFilteredColumnStats } from '@/hooks/useColumnStats';
import { setColumnFilterAndRefreshData } from '@/store/agColDefsSlice';
import { useSingleColumnMeta } from '@/hooks/useApiData';

const EnumFilter = React.memo(({ name }) => {
  const dispatch = useDispatch();

  const baselineColumnStats = useBaselineColumnStats(name);
  const filteredColumnStats = useFilteredColumnStats(name);
  const meta = useSingleColumnMeta(name);


  const reduxFilters = useSelector(state => state.agColDefs.filters[name]);


  const allValues = baselineColumnStats?.data?.cnt_values.map(({value}) => value);

  const initialBufferFilters = useMemo(() => ({
    including: reduxFilters?.including || allValues,
    null: reduxFilters?.null || 'include',
    clickedId: null
  }), [reduxFilters, allValues]);

  const [bufferFilters, setBufferFilters] = useState(initialBufferFilters);




  const applyFilters = useCallback(() => {
    const filter = {
      including: bufferFilters.including,
      null: !meta.hasNull ? 'N/A' : bufferFilters.null,
      filterType: 'myEnum'
    }
    setColumnFilterAndRefreshData(dispatch, name, filter);
  }, [bufferFilters.including, bufferFilters.null, meta.hasNull, dispatch, name]);

  const handleClickedIdChangeInTreeMap = useCallback((value) => {
    if (value !== null) {
      setBufferFilters({...bufferFilters, null: 'exclude', including: [value], clickedId: value});
    } else {
      setBufferFilters({...bufferFilters, clickedId: null});
    }
  }, [bufferFilters, setBufferFilters]);

  const cachedSetBufferFilters = useCallback((newBufferFilters) => {
    setBufferFilters({...bufferFilters, ...newBufferFilters});
  }, [bufferFilters]);

  const handleNullChange = useCallback((value) => {
    if (value === 'only') {
      setBufferFilters({clickedId: null, including: [], null: value});
    } else {
      if (bufferFilters.including.length === 0) {
        setBufferFilters({clickedId: null, including: allValues, null: value});
      } else {
        setBufferFilters({...bufferFilters, null: value});
      }
    }
  }, [bufferFilters, allValues, setBufferFilters]);

  const cancelChanges = useCallback(() => {
    setBufferFilters(initialBufferFilters);
  }, [initialBufferFilters]);


  return (

      <Box sx={{height: '100%', display: 'flex', flexDirection: 'column', gap: 1}}>

        <BorderedBox sx={{display: 'flex', flexDirection: 'column', gap: 1}}>
          <StatsCount columnName={name}/>
          {!meta.hasNull ? null : <NullFilterRadio filterNull={bufferFilters.null} setFilterNull={handleNullChange}/>}
        </BorderedBox>

        <Box sx={{height: 100}}>
            <MyTreeMap columnStats={filteredColumnStats.data} setClickedId={handleClickedIdChangeInTreeMap}/>
        </Box>

        <Box sx={{width: '100%', flex: 1, display: 'flex'}}>
          <EnumFilterTable
            name={name}
            bufferFilters={useMemo(() => bufferFilters, [bufferFilters])}
            setBufferFilters={cachedSetBufferFilters}
           />
        </Box>

        <FilterCardBottomButtons columnName={name} cancelChanges={cancelChanges} applyFilters={applyFilters} />
      </Box>

 );
});

EnumFilter.displayName = 'EnumFilter';

export default EnumFilter;