import React, { useState, useRef, useEffect } from 'react';
import Box from '@mui/material/Box';

export default function HorizontalSplitBox({left, right, initLeftWidth='50%'}) {
  const [leftWidth, setLeftWidth] = useState(initLeftWidth);
  const [containerWidth, setContainerWidth] = useState(0);
  const containerRef = useRef(null);

  // Track container width for percentage calculations
  useEffect(() => {
    const updateContainerWidth = () => {
      if (containerRef.current) {
        setContainerWidth(containerRef.current.clientWidth);
      }
    };

    updateContainerWidth();

    const resizeObserver = new ResizeObserver(updateContainerWidth);
    if (containerRef.current) {
      resizeObserver.observe(containerRef.current);
    }

    return () => resizeObserver.disconnect();
  }, []);

  // Convert percentage to pixels if needed
  const getPixelWidth = (width) => {
    if (typeof width === 'string' && width.includes('%')) {
      const percentage = parseFloat(width) / 100;
      return containerWidth * percentage;
    }
    return parseFloat(width);
  };

  // Convert pixels back to percentage if original was percentage
  const updateWidth = (newPixelWidth) => {
    if (typeof initLeftWidth === 'string' && initLeftWidth.includes('%')) {
      const percentage = (newPixelWidth / containerWidth) * 100;
      setLeftWidth(`${Math.max(10, Math.min(90, percentage))}%`); // Clamp between 10% and 90%
    } else {
      setLeftWidth(Math.max(100, newPixelWidth)); // Min 100px for pixel values
    }
  };

  const startDrag = (e) => {
    const startX = e.clientX;
    const startPixelWidth = getPixelWidth(leftWidth);

    const onMouseMove = (e) => {
      const delta = e.clientX - startX;
      const newPixelWidth = startPixelWidth + delta;
      updateWidth(newPixelWidth);
    };

    const onMouseUp = () => {
      document.removeEventListener('mousemove', onMouseMove);
      document.removeEventListener('mouseup', onMouseUp);
    };

    document.addEventListener('mousemove', onMouseMove);
    document.addEventListener('mouseup', onMouseUp);
  };

  const currentPixelWidth = getPixelWidth(leftWidth);

  return (
    <Box
      ref={containerRef}
      sx={{
        height: '100%',
        width: '100%',
        display: 'flex',
        flexDirection: 'row',
        overflow: 'auto',
        maxHeight: '100%',
        minHeight: 0,
      }}
    >
      {/* Left Pane */}
      <Box sx={{
        width: `${currentPixelWidth}px`,
        overflow: 'auto',
        maxHeight: '100%',
        minHeight: 0,
      }}>
        {left}
      </Box>

      {/* Divider */}
      <Box
        onMouseDown={startDrag}
        sx={{
          width: '2px',
          bgcolor: 'divider',
          cursor: 'col-resize',
          '&:hover': { bgcolor: 'primary.main' },
        }}
      />

      {/* Right Pane */}
      <Box sx={{
        flex: 1,
        overflow: 'auto',
        maxHeight: '100%',
        minHeight: 0,
      }}>
        {right}
      </Box>
    </Box>
  );
}