import {
    Table, TableBody, TableCell, TableContainer, TableRow,
    Paper, Stack, Chip, Typography, Box
} from '@mui/material'
import React from 'react';
import { SX_TEXT_HOVER } from '@/utils/styles';

const KeyChip = ({ label }) => (
    <Chip
    label={label}
    size="small"
    sx={{
        fontFamily: 'monospace',
        borderRadius: '6px',
        bgcolor: 'background.card',
    }}
    variant="outlined"
    />
)

export const ShortcutRow = React.memo(function ShortcutRow({ keys, descriptions }) {
    return (
    <TableRow>
        <TableCell sx={{width: '100px', textAlign: 'right'}}>
            <Stack direction="row" spacing={1} alignItems="center">
                {keys.map((key, i) => (
                    <KeyChip key={i} label={key} />
                ))}
            </Stack>
        </TableCell>
        <TableCell>
        <Box sx={{display: 'flex', flexDirection: 'column', gap: 1}}>
            {descriptions.map((description, i) => (
                <Typography key={i} variant="body2" sx={{...SX_TEXT_HOVER}}>
                    {description}
                </Typography>
            ))}
        </Box>
        </TableCell>
    </TableRow>
    )
});


export const KeyboardShortcutsTable = React.memo(function KeyboardShortcutsTable({children}) {
    return (
        <TableContainer component={Paper}>
        <Table>
        <TableBody>
            {children}
        </TableBody>
        </Table>
        </TableContainer>
    )
});

KeyboardShortcutsTable.displayName = 'KeyboardShortcutsTable';