import { Box, IconButton } from '@mui/material';
import React from 'react';
import ClickCard from '@/components/elements/ClickCard';
import { SX_TEXT_HOVER } from '@/utils/styles';

export const BottomIconCard = ({
  title, children, icon, transformOriginHorizontal='right',
   width=300, height=800, initMaximized=false, color='inherit'}) => {
    const styledIcon = (
        <IconButton size="small" color={color} sx={color === 'inherit' ? SX_TEXT_HOVER: {}} >
            {React.cloneElement(icon, {fontSize: 'small'})}
        </IconButton>
    );
    return (
      <Box sx={{flex: 0}}>
        <ClickCard
          trigger={styledIcon}
          title={title}
          tooltip={title}
          height={height}
          width={width}
          initMaximized={initMaximized}
          anchorVertical="top"
          anchorHorizontal="right"
          transformOriginVertical="bottom"
          transformOriginHorizontal={transformOriginHorizontal}
          >
            <Box sx={{display: 'flex', flexDirection: 'column', gap: 2, width: '100%', height: '100%'}}>
              {children}
            </Box>
        </ClickCard>
      </Box>
    );
  };



