import { sqlElements } from '@/utils/sql/sql';
import _ from 'lodash';
import { setRowDataSource } from './viewingSlice';
import { runQuery } from '@/utils/api';
import { initialState } from '@/store/dataSlice';
import { rowDataSlice } from '@/store/dataSlice';
import { evaluateAllExpressionsForAllRows } from '@/store/derivedColumnsSlice';
import { batchProxyUrlApi } from '@/utils/api';
import { pickColumnsByRenderTypeList } from '@/hooks/useColumnPreference';
import { RENDER_TYPES } from '@/constants';

const proxyAllUrlsInRowData = async ({getState, rowData}) => {
  // Sign get urls for rowData
  const urlColumns = pickColumnsByRenderTypeList({state: getState(), renderTypeList: [
    RENDER_TYPES.IMAGE_URL, RENDER_TYPES.VIDEO_URL
  ]});
  const promises =  _.map(urlColumns, col => {
    const urls = _.map(rowData, r => r[col]);
    return batchProxyUrlApi({urls});
  });
  const proxiedUrls = await Promise.all(promises);
  const urlDataMap = _.zipObject(urlColumns, proxiedUrls);
  _.map(rowData, (r, i) => {
    _.map(urlColumns, (col) => {
      r[col] = urlDataMap[col][i];
    });
  });
  return rowData;
}

export const fetchRowData = () => async (dispatch, getState) => {
  const { data, loading, error, shouldRefresh } = getState().rowData || initialState;
  if ((data || loading || error) && !shouldRefresh) return; //  Already loading or loaded  skip!

  dispatch(rowDataSlice.actions.fetchStart());

  try {
    const elements = sqlElements(getState());

    const sql = `SELECT * ${elements.from} ${elements.where} ${elements.order} ${elements.pagination}`;
    const rawRowData = await runQuery({dispatch, key: 'rowData', query: sql});
 ///   const rowData = rawRowData;
    const rowData = await proxyAllUrlsInRowData({getState, rowData: rawRowData});

    // Add derived columns
    const derivedColumns = getState().derivedColumns;
    const enrichedRowData = await evaluateAllExpressionsForAllRows({derivedColumns, rowData});

    dispatch(rowDataSlice.actions.fetchSuccess(enrichedRowData));
    dispatch(setRowDataSource('Table'));

  } catch (err) {
    if (err.stack) {
      console.error('Stack trace:', err.stack);
    }
    dispatch(rowDataSlice.actions.fetchError(err.message));
  }
};

export const fetchRandomRowData = ({source}) => async (dispatch, getState) => {
  const { data, loading, error, shouldRefresh } = getState().rowData || initialState;
  if ((data || loading || error) && !shouldRefresh) return; //  Already loading or loaded  skip!

  dispatch(rowDataSlice.actions.fetchStart());

  try {
    const {from, where} = sqlElements(getState());
    const pageSize = getState().viewing.pagination.pageSize;
    const sampleCondition = getState().viewing.sampleCondition || 'True';

    const sql = `
    SELECT *
    FROM (SELECT * ${from} ${where} AND (${sampleCondition}))
    USING SAMPLE RESERVOIR(${pageSize} ROWS);
    `;
    const rawRowData = await runQuery({dispatch, key: 'samples', query: sql});
    const rowData = await proxyAllUrlsInRowData({getState, rowData: rawRowData});

    // Add derived columns
    const derivedColumns = getState().derivedColumns;
    const enrichedRowData = await evaluateAllExpressionsForAllRows({derivedColumns, rowData});

    dispatch(rowDataSlice.actions.fetchSuccess(enrichedRowData));
    dispatch(setRowDataSource(source));
  } catch (err) {
    console.error(err);
    dispatch(rowDataSlice.actions.fetchError(err.message));
  }

}