'use client';
import { RenderTypeIcon } from '@/components/IconControls/RenderTypeIcon';
import { DropdownColumnPinIcon } from '@/components/IconControls/ColumnPinIcon';
import ColumnHideIcon from '@/components/IconControls/ColumnHideIcon';

import { Box } from '@mui/material';
import React from 'react';
import { CopyToClipboardButton } from '@/components/elements/Controls';



export const TopButtons = React.memo(function TopButtons({name, value, colDef, showCopy=true}) {


  return (
    <Box sx={{display: 'flex', flexDirection: 'row', gap: 1, alignItems: 'center', padding: 1}}>
      {showCopy && <CopyToClipboardButton value={value} />}
      <ColumnHideIcon columnName={name} hide={colDef?.hide}/>
      <RenderTypeIcon columnName={name} />
      <DropdownColumnPinIcon columnName={name} pinned={colDef?.pinned}/>

    </Box>
  )

});


