
import { JSONTree } from 'react-json-tree';
import { Typography, Paper } from '@mui/material';
import { useSelector } from 'react-redux';
import CellRightClickCard from '@/components/table/rightClickMenu/CellRightClickCard';
import { Box } from '@mui/material';
import { CopyToClipboardButton, BooleanIconButton } from '@/components/elements/Controls';
import { useState } from 'react';
import TitleIcon from '@mui/icons-material/Title';
import SortByAlphaIcon from '@mui/icons-material/SortByAlpha';

const valueRenderer = (raw, value) => {
  const type = typeof value === 'object' && value === null
    ? 'null'
    : Array.isArray(value)
    ? 'array'
    : typeof value;
  return (
    <span>
      {raw} <span style={{ color: '#888', fontStyle: 'italic' }}>({type})</span>
    </span>
  );
};


export const JsonBox = ({ obj }) => {
  const jsonString = JSON.stringify(obj);
  const [showType, setShowType] = useState(false);
  const [sorted, setSorted] = useState(false);
  const jsonViewCollectionLimit = useSelector(state => state.settings.jsonViewCollectionLimit) || 10;

   return (
    <Paper sx={{ p: 0, width: '100%', height: '100%', overflow: 'auto' }}>
      <Box sx={{display: 'flex', flexDirection: 'row', gap: 1, alignItems: 'center'}}>
        <CopyToClipboardButton value={jsonString} />
        <BooleanIconButton
          iconOn={<TitleIcon />}
          iconOff={<TitleIcon />}
          value={showType}
          onClick={() => setShowType(!showType)}
          tooltip="Show type"
        />
        <BooleanIconButton
          iconOn={<SortByAlphaIcon />}
          iconOff={<SortByAlphaIcon />}
          value={sorted}
          onClick={() => setSorted(!sorted)}
          tooltip="Sort keys"
        />

      </Box>

      <JSONTree
          data={obj}
          hideRoot={true}
          collectionLimit={jsonViewCollectionLimit}
          valueRenderer={showType ? valueRenderer : undefined}
          sortObjectKeys={sorted}
        />
    </Paper>
   );
};


export const MiniJsonBox = ({ obj }) => {
  const tableMaxRowHeight = useSelector(state => state.settings.tableMaxRowHeight);
  if (!obj) {
    return <Typography variant="body2">{obj}</Typography>;
  }


  return (
    <Box>
      <Typography variant="body2" color="inherit" sx={{
        overflow: 'hidden', maxHeight: tableMaxRowHeight, whiteSpace: 'normal',
      }}>
        {JSON.stringify(obj)}
      </Typography>

      <CellRightClickCard sx={{width: 400}}>
        <JsonBox obj={obj} />
        </CellRightClickCard>
    </Box>
  );
};


