import React from 'react';
import { Box } from '@mui/material';
import { CircularProgress } from '@mui/material';
import CodeMirror from '@uiw/react-codemirror';
import { python } from '@codemirror/lang-python';
import { sql } from '@codemirror/lang-sql';
import { markdown } from '@codemirror/lang-markdown';
import { useSelector } from 'react-redux';
import { oneDark } from '@codemirror/theme-one-dark';
import path from 'path-browserify';
import { JsonBox } from '@/components/elements/JsonBox';
import YAML from 'yaml';
import { getFileUrl } from '@/utils/urls';

const TextContentViewer = React.memo(function TextContentViewer({fullPath}) {
    const theme = useSelector((state) => state.settings.mode);
    const [content, setContent] = React.useState('');
    const [isLoading, setIsLoading] = React.useState(true);
    const ext = path.extname(fullPath);

    React.useEffect(() => {
        setIsLoading(true);
        fetch(getFileUrl(fullPath))
            .then(res => res.text())
            .then(text => setContent(text))
            .then(() => setIsLoading(false))
            .catch(err => console.error('Error loading file content:', err));
    }, [fullPath]);

    if (isLoading) {
        return (
            <Box sx={{display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100%'}}>
                <CircularProgress />
            </Box>
        );
    } else if (['.json'].includes(ext)) {
        try {
            const obj = JSON.parse(content);
            return <JsonBox obj={obj} />;
        } catch (err) {
            return <Box sx={{p: 2}}>{content}</Box>
        }
    } else if (['.yaml', '.yml'].includes(ext)) {
        try {
            const obj = YAML.parse(content);
            return <JsonBox obj={obj} />;
        } catch (err) {
            return <Box sx={{p: 2}}>{content}</Box>
        }
    } else {
        const extensionMap = {
            '.py': python(),
            '.sql': sql(),
            '.md': markdown(),
        }

        return (
            <Box sx={{}}>
                <CodeMirror
                    height="100%"
                    style={{height: '100%'}}
                    value={content}
                    editable={false}
                    extensions={[extensionMap[ext]]}
                    theme={theme === 'dark' ? oneDark : 'light'}
                />
            </Box>
        );
    }



});

export default TextContentViewer;