import React from 'react';
import { Box } from '@mui/material';
import ViewAlbum from '@/components/folderBrowser/folderViewers/ViewAlbum';
import _ from 'lodash';
import path from 'path-browserify';
import { imageExtensions } from '@/utils/typesUtils';
import { useSelector, useDispatch } from 'react-redux';
import { MyDropdown } from '@/components/elements/Controls';
import { setViewFolderAs } from '@/store/folderBrowserSlice';
import { useEffect } from 'react';
import { isMissing } from '@/utils/helpers';
import FolderPeek from '@/components/folderBrowser/folderViewers/FolderPeek';
import ChildrenStats from '@/components/folderBrowser/folderViewers/ChildrenStats';
import BreadCrumbsNav from '@/components/folderBrowser/BreadCumbsNav';
import { pathJoin, parentName } from '@/store/folderBrowserSlice';


const FolderViewer = React.memo(function FolderViewer({viewingItem}) {
    const dispatch = useDispatch();
    const { id, label, children: items } = viewingItem;
    const rootFolder = useSelector(state => state.settings.folderBrowserRootFolder);
    const countFiles = _(items).filter(['isDir', false]).countBy(it => path.extname(it.id)).value();
    const hasParquet = _.keys(countFiles).includes('.parquet');
    const hasImage = _.keys(countFiles).some(ext => imageExtensions.includes(ext));

    const viewAsOptions = [
        'default',
        ...(hasParquet ? ['ParquetFolder'] : []),
        ...(hasImage ? ['ImageFolder'] : [])
    ];
    const viewAs = useSelector(state => state.folderBrowser.viewFolderAs[id]);

    useEffect(() => {
        if (isMissing(viewAs) && _.size(countFiles) === 1) {
            const ext = Object.keys(countFiles)[0];
            if (imageExtensions.includes(ext)) {
                dispatch(setViewFolderAs({id, viewAs: 'ImageFolder'}));
            } else if (ext === '.parquet') {
                dispatch(setViewFolderAs({id, viewAs: 'ParquetFolder'}));
            }
        }
    }, [id, countFiles, dispatch, viewAs]);


    const mainView = () => {
        switch (viewAs || 'default') {
            case 'ImageFolder':
                return <ViewAlbum photoList={items.map(it => pathJoin(rootFolder, it.id))} />;
            case 'ParquetFolder':
                return 'TODO';
            default:
                return <FolderPeek items={items} />;
        }
    }

    return (
    <Box sx={{
        width: '100%', height: '100%', display: 'flex', flexDirection: 'column',
        padding: 2, gap: 2, alignItems: 'left', backgroundColor: 'background.paper'
    }}>
        <Box sx={{display: 'flex', flexDirection: 'row', gap: 2, alignItems: 'center'}}>
            <BreadCrumbsNav id={id} isDir={true}/>
            <Box sx={{flex: 1}}></Box>

            <MyDropdown
                options={viewAsOptions}
                value={viewAs || 'default'}
                label="View as"
                width="200px"
                onChange={(newValue) => {
                    dispatch(setViewFolderAs({id, viewAs: newValue}));
                }}
            />


        </Box>
        <Box sx={{border: '1px solid', borderColor: 'divider', borderRadius: 1, padding: 1}}>
            <ChildrenStats items={items} />
        </Box>

        <Box sx={{flex: 1, overflowY: 'auto'}}>
            {mainView()}
        </Box>
    </Box>
    );
});

export default FolderViewer;