import React, { useEffect } from 'react';
import { Box, CircularProgress, Typography } from '@mui/material';
import { fetchParquetMeta } from '@/utils/api';
import { useDispatch } from 'react-redux';
import { BasicTable } from '@/components/elements/BasicTable';
import { useSelector } from 'react-redux';
import SqlCard from '@/components/elements/SqlCard';
import { MyButton } from '@/components/elements/Controls';
import useMUITheme from '@/hooks/useMUITheme';
import Link from 'next/link';
import { ROUTES } from '@/constants';
import OpenInNewOutlinedIcon from '@mui/icons-material/OpenInNewOutlined';

const NumberCell = React.memo(function NumberCell({value}) {
    const valueStr = value === null ? '' : value.toLocaleString();
    return <Typography variant="body1">{valueStr}</Typography>;
});

const StringCell = React.memo(function StringCell({value}) {
    const valueStr = value === null ? '' : value;
    return <Typography variant="body1" sx={{overflowWrap: 'anywhere', whiteSpace: 'normal'}}>
        {valueStr}
        </Typography>;
});

const ParquetPreview = React.memo(function ParquetPreview({fullPath}) {
    const dispatch = useDispatch();
    const sqlKey = 'parquetMeta';
    const sql = useSelector((state) => state.sql[sqlKey]);
    const muiTheme = useMUITheme();

    useEffect(() => {
       fetchParquetMeta({dispatch, dataFile: fullPath, sqlKey});
    }, [fullPath, dispatch, sqlKey]);

    if (sql?.status === 'running') {
        return <CircularProgress />;
    }

    if (!sql || !sql.data) {
        return null;
    }



    const overridingColDefs = {
        column_name: {
            pinned: 'left',
            cellStyle: {
                color: muiTheme.palette.text.primary,
            },
        },
        column_type: {
            cellStyle: {
                fontFamily: 'monospace',
                whiteSpace: 'normal',
                wordBreak: 'break-word',
            },
        },
        cntAll: {
            cellRenderer: params => <NumberCell value={params.value} />,
        },
        cntNull: {
            cellRenderer: params => <NumberCell value={params.value} />,
        },
        min: {
            cellRenderer: params => <StringCell value={params.value} />,
        },
        max: {
            cellRenderer: params => <StringCell value={params.value} />,
        },

    };

    return (
        <Box sx={{display: 'flex', flex: 1, flexDirection: 'column', gap: 2, height: '100%', width: '100%'}}>
            <Box sx={{flex: 0, display: 'flex', flexDirection: 'row', gap: 2, alignItems: 'center'}}>
                <Typography variant="h6">Previewing columns metadata from the parquet file</Typography>
                <SqlCard sqlKey={sqlKey} />
                <Box sx={{flex: 1}}></Box>
                <Link target="_blank" href={`${ROUTES.Table}?filePath=${fullPath}&fileFormat=parquet`}>
                    <MyButton>Open <OpenInNewOutlinedIcon/></MyButton>
                </Link>
            </Box>
            <Box sx={{flex: 1}}>
                <BasicTable initRowData={sql.data} overridingColDefs={overridingColDefs} />
            </Box>
        </Box>
    );
});

export default ParquetPreview;