'use client';
import { Box } from '@mui/material';
import { proxyedUrl } from '@/utils/urls';
import React from 'react';
import { AutoLink } from '@/components/elements/AutoLink';


export const ImageBox = React.memo(function ImageBox({url, size=500, cropSquare=false, showUrl=false}) {
  const proxyUrl = proxyedUrl(url);
  const imageStyle = cropSquare ? {
    objectFit: 'cover', width: size, height: size
  } : {
    objectFit: 'contain', width: '100%', height: '100%'
  };

  return (
    <Box sx={{display: 'flex', flexDirection: 'column', gap: 1, maxWidth: size, maxHeight: size}}>
      {showUrl && <AutoLink url={url} />}
      <img src={proxyUrl} alt={url} style={imageStyle} />

    </Box>
  );
});