import { PieChart, Pie, Cell } from 'recharts';
import { stringToColor } from '@/utils/colors';
import React from 'react';
import { isMissing } from '@/utils/helpers';
import { useFilteredColumnStats } from '@/hooks/useColumnStats';
import { useSelector } from 'react-redux';
import { CircularProgress } from '@mui/material';

const NullPie = React.memo(({columnName}) => {
    const heightOfColumnDistribution = useSelector(state => state.settings.heightOfColumnDistribution);
    const size = Math.min(heightOfColumnDistribution - 2, 48);

    const { data } = useFilteredColumnStats(columnName) || {};

    if (isMissing(data)) {
        return <CircularProgress size={size}/>;
    }

    const nullPercentage = 100 * data.cnt_null / data.cnt_all;

    const nullStatsData = [{
        value: 'null', percent: nullPercentage
    }, {
        value: 'Not null', percent: 100 - nullPercentage
    }];


    return (
        <PieChart width={size} height={size} style={{margin: 0, pointerEvents: 'none', userSelect: 'none'}}>
            <Pie data={nullStatsData}
                innerRadius={0}
                outerRadius={size / 2 - 2}
                paddingAngle={0}
                dataKey="percent"
                nameKey="value"
                isAnimationActive={false}
                onClick={null}
                >
                {nullStatsData.map((entry, index) => {
                    const {color, backgroundColor} = stringToColor(entry.value);
                    return <Cell key={`cell-${index}`} fill={backgroundColor} color={color} />
                })}

            </Pie>
        </PieChart>
    );
});

NullPie.displayName = 'NullPie';

export default NullPie;






